<template>
  <div class="logisticsInformation">
		<headhistory :data='title' v-if='true'></headhistory>
    <div class='logisticsInformation-item'>
      <div class='logisticsInformation-item-content'>
        <div class='logisticsInformation-item-left'>快递单号:</div>
        <div class='logisticsInformation-item-right'>{{orderNum}}</div>
      </div>
      <div class='logisticsInformation-item-content'>
        <div class='logisticsInformation-item-left'>承运快递:</div>
        <div class='logisticsInformation-item-right'>{{name}}</div>
      </div>
    </div>
    
    
    <!--时间线-->
    <div class='time-line'>
      <div v-for='(item,index) in data' class='time-line-item'>
        <div class='time-line-item-context'>{{item.context}}</div>
        <div class='time-line-item-time'>{{item.time}}</div>
    </div>
    </div>
    
    <login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
import login from'../login/login'
import { Toast } from 'mint-ui';
export default {
  name: 'logisticsInformation',
  data () {
    return {
			title:'物流信息',
      data:'',
      orderNum:'暂无物流信息',
      name:'暂无物流信息',
      showFlag:false
    }
  },
	components:{
		headhistory,
		login
	},
	created(){
    if(sessionStorage.jwt == undefined || sessionStorage.jwt == 'undefined' || sessionStorage.jwt.length == 0){
      this.login();
      return false
    }
		this.getData();
	},
  watch:{
    
  },
	methods:{
		getData(){
      this.axios({
        url:'shopOrderExpress/'+this.$route.query.order_sn
      }).then(res => {
        console.log(res);
        let data = res.data.data;
        if(data.showapi_res_body.msg == '查询成功'){
          this.data = data.showapi_res_body.data;
          this.orderNum = data.showapi_res_body.mailNo;
          this.name = data.showapi_res_body.expTextName;
        }else{
          Toast({
            message:data.showapi_res_body.msg,
            duration:1000
          });
        }
      })
      
    },
    login (data) {
      this.showFlag = !this.showFlag;
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.logisticsInformation{
  padding-top:12vw;
  background:#f2f2f2;
  
  
  .logisticsInformation-item{ //快递信息
    background:#fff;
    margin-bottom:2vw;
    padding:2vw;
    
    .logisticsInformation-item-content{ //快递信息详情
      display:flex;
      font-size:4vw;
      margin:2vw 0vw;
      
      .logisticsInformation-item-left{ //标题
        width:20vw;
        
      }
      
      .logisticsInformation-item-right{ //信息
        color:#555;
      }
      
    }
    
  }
  
  .time-line{ //时间线
    background:#fff;
    padding:2vw 2vw 0 2vw;
    overflow:hidden;
    
    .time-line-item{ //时间线详情
      margin-bottom: 2vw;
      position:relative;
      display:flex;
      justify-content:center;
      align-items:flex-start;
      flex-flow: column wrap;
      padding-left:5.5vw;
      
      &:after{ //时间线的点
        content:'';
        width:3vw;
        height:3vw;
        background:#fff;
        position:absolute;
        left:0vw;
        border-radius:50%;
        border:1px solid #aaa;
      }
      
      
      &:before{ //时间线的线
        content:'';
        width:1px;
        height:120%;
        background:#aaa;
        position:absolute;
        left:1.5vw;
        top:0%;
      }
      
      .time-line-item-context{ //时间线信息
        font-size:4vw;
        display: -webkit-box;
        word-break: break-all;
        -webkit-box-orient: vertical;
        
      }
      
      .time-line-item-time{ //时间线时间
        color:#999;
        font-size:3.5vw;
        border-bottom:1px solid #eee;
        padding-bottom:1vw;
        margin-top:1vw;
        width:100%;
      }
      
    }
    
  }
  
}


</style>



