<template>
  <div v-if='goods' class="chooseAddress">
		<div class='chooseAddress-item' @click='show'>
      <div class='chooseAddress-firstname'>
        {{data.name[0]}}
      </div>
      <div class='chooseAddress-items'>
        <div class='chooseAddress-infomation'>
          <div class='chooseAddress-name'>收货人:{{data.name}}</div>
          <div class='chooseAddress-pgone'>{{data.phone}}</div>
        </div>
        <div class='chooseAddress-address'>收货地址:{{data.province + data.city + data.area + data.street + data.address}}</div>
      </div>
    </div>
    <div v-show='layerFlag' class='mask'>
      <div class='layer'>
        <div class='layer-item'>
          <div v-for='item in goods' @click='choose(item)'>
            <div class='item-content'>
              <div class='item-information'>
                <div class=''>收货人:{{item.name}}</div>
                <div class=''>{{item.phone}}</div>
              </div>
              <div class='item-address'>收货地址:{{item.province+item.city+item.area+item.street+item.address}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { MessageBox } from 'mint-ui';
export default {
  name: 'chooseAddress',
	props:['cartdata'],
  data () {
    return {
			goods:'',
      data:'',
      layerFlag:false
    }
  },
	components:{
		
	},
	methods:{
    getAddress(){
      this.axios({
        url:"shopContact"
      }).then(res => {
        if(res.data.status == 200){
          this.goods = res.data.data;
        }else if(res.data.status == 204){
          MessageBox.confirm('你还没有设置收获地址,是否设置收获地址?').then(action => {
            this.$router.push({name:'address'})
          });
        }
      })
    },
    choose(data){
      console.log(data)
      this.data = data;
      this.layerFlag = false;
      this.$emit('changeaddress',data);
    },
    show(){
      if(this.layerFlag){
        this.layerFlag = false;
      }else if(!this.layerFlag){
        this.layerFlag = true;
      }
    }
	},
	created(){
		this.getAddress()
	},
  watch:{
    goods(val){
      this.$nextTick(()=>{
				let wrapper = document.querySelector('.layer');
				wrapper.style.height = '65vh';
				let scroll = new BScroll(wrapper,{
					click:true
				})
			})
      this.data = val[0];
      this.$emit('changeaddress',val[0]);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.chooseAddress{
  background:#fff;
  display:flex;
  flex-flow:row wrap;
  align-items:center;
  justify-content:flex-start;
  
  .chooseAddress-item{
    display:flex;
    flex-flow:row nowrap;
    justify-content:center;
    align-items:center;
    
    .chooseAddress-items{
      display:flex;
      flex-flow:column wrap;
      justify-content:flex-start;
      align-items:flex-start;
      flex:1;
      
      .chooseAddress-infomation{
        display:flex;
        flex-flow:row wrap;
        align-items:center;
        justify-content:space-between;
        margin-bottom:1vw;
        margin-top:2vw;
      }
      
      .chooseAddress-address{
        font-size:3.5vw;
        margin-bottom:2vw;
      }
      
    }
    
    .chooseAddress-firstname{
      width:10vw;
      height:10vw;
      margin:1vw;
      text-align:center;
      border-radius:50%;
      background:red;
      display:flex;
      align-items:center;
      justify-content:center;
      color:#fff;
      background-color:#bec0c2;
    }
    
  }
  
}
.layer,.layer-item{
  overflow:hidden;
}
.item-content{
  display:flex;
  flex-flow:column wrap;
  margin-bottom:1vw;
  padding-bottom:1vw;
  border-bottom:1px solid #eee;
  .item-information{
    display:flex;
    flex-flow:row wrap;
    justify-content:space-between;
    font-size:3.5vw;
  }
  .item-address{
    font-size:3.5vw;
  }
}
</style>



