<template>
  <div class="detail-footer">
		<!--<div class='detail-go' @click.stop='go("index")'>
			<img class='detail-go-img' src='/public/image/goodsDetails/footer/shop.png'>
			<div>店铺</div>
		</div>-->
		<div class='detail-go' @click.stop='go("service")'>
			<img class='detail-go-img' src='/public/image/goodsDetails/footer/在线客服1.png'>
			<div>客服</div>
		</div>
		<!--<div class='detail-go'>
			<img class='detail-go-img' src='/public/image/goodsDetails/footer/collection.png'>
			<div>收藏</div>
		</div>-->
		<div class='detail-operation'>
			<div class='detail-cart' @click.stop='show("add")'>加入购物车</div>
			<div class='detail-buy' @click.stop='show("buy")'>立即购买</div>
		</div>
		<div v-if='layerFlag' class='mask'>
			<div class='layer'>
        <img src='/public/image/input/close.png' class='close' @click.stop='close'>
				<div class='detail-layer-content'>
					<img :src='imgUrl' class='detail-layer-img'>
					<div class='detail-layer-informaition'>
						<div class='detail-layer-price'>{{price}}</div>
						<div class='detail-layer-sales'>库存{{sales}}件</div>
						<div class='detail-layer-choose'>
              <span class='detail-layer-choose-item' v-for='item in data'>{{item.text[item.flag]}}</span>
            </div>
					</div>
				</div>
				<div class='detail-layer-classification' ref='scrolly'>
					<div class='detail-layer-classification-scroll'>
						<div 
              class='detail-layer-classification-item' 
              v-for='(item,key,index) in data' 
              v-if='item.key != ""'
              :key='index'
            >
							<div class='detail-layer-classification-title'>{{item.key}}</div>
							<div class='flex-row-wrap'>
								<div @click.stop='showlist(item,index)' :class='item.flag == index ? "select":""' class='detail-layer-classification-list' v-for='(list,index) in item.text' :key='index'>{{list}}</div>
							</div>
						</div>
						<div class='flex-row-between detail-layer-operation'>
							<div>购买数量</div> 
							<div class='detail-layer-operation-item flex-row-wrap'>
								<div class='operation-btn' @click.stop='subtract'>-</div>
								<div>{{num}}</div>
								<div class='operation-btn' @click.stop='add'>+</div>
							</div>
						</div>
					</div>
				</div>
				
				<div class='detail-layer-btn-item'>
					<div  class='detail-cart' @click='addShopCart()'>加入购物车</div>
					<div  class='detail-buy' @click='buyGoods()'>购买</div>
				</div>
			</div>
		</div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { Toast } from 'mint-ui';
export default {
  name: 'goodsDetail-footer',
  data () {
    return {
			layerFlag:false,
			num:1,
      imgUrl:'', //图片地址
      price:'', //价格
      sales:'', //库存
      stock_id:'', //库存id
      data:'',
      buyStatus:'add',
      goodsname:'',
      weight:''
    }
  },
	watch:{
		layerFlag(val,oldVal){
			if(val){
				this.$nextTick(()=>{
					let wrapper = document.querySelector('.detail-layer-classification');
					wrapper.style.height = '52vh';
					let scroll = new BScroll(wrapper,{
						click:true
					})
				})
			}
		}
	},
  created(){
    this.axios({ //获取商品信息
      method:'get',
      url:`shopGoods/${this.$route.query.id}`,
      params:{
        key:sessionStorage.shopkey
      }
    }).then((res)=>{
      console.log('数据',res.data.data)
      if(res.data.status == 200){
        this.goodsname = res.data.data.name;
        this.imgUrl = res.data.data.stock[0].pic_url;
        try{
          this.data = {
            property1:{
              text:res.data.data.property1.split(':')[1].split(','),
              flag:0,
              index:1,
              key:res.data.data.property1.split(':')[0]
            },
            property2:{
              text:res.data.data.property2.split(':')[1].split(','),
              flag:0,
              index:2,
              key:res.data.data.property2.split(':')[0]
            }
          }
        }catch(err){
          this.data = {
            property1:{
              text:res.data.data.property1.split(':')[1].split(','),
              flag:0,
              index:1,
              key:res.data.data.property1.split(':')[0]
            },
            property2:{
              text:'',
              flag:0,
              index:2,
              key:''
            }
          }
        }
        
        
        //默认获取第一条第二个数据
        try{
          let data = this.data.property1.text[this.data.property1.flag]
          this.getProperty2(data);
        }catch(err){
          console.log('获取数据报错')
        }
        
        //获取重量
        try{
          this.weight = res.data.data.stock[0].weight
        }catch(err){
          
        }
        
        //获取选中数据大体信息
        let property_name = [];
        for(let i in this.data){
          property_name.push(this.data[i].text[this.data[i].flag])
          console.log(this.data[i].text);
        }
        this.getGoodsClassData(property_name);
      }
      
    })
	},
	methods:{
		go(data){ //跳转页面(店铺首页之类的)
      if(data == 'service') location = `http://chat.juanpao.com/index/index/home?visiter_id=&visiter_name=&avatar=&business_id=${sessionStorage.shopkey}&groupid=0`
			
		},
		show(data){ //是否展示商品分类
			this.layerFlag = true;
      this.buyStatus = data;
		},
    close(){
      this.layerFlag = false;
    },
		showlist(data,index){ //设置选中,同时获取对应参数
      data.flag = index;
      if(data.index == 1){
        this.getProperty2(data.text[index]); //获取第二参数可用数据
      }
      let property1_name = [];
      for(let i in this.data){
        property1_name.push(this.data[i].text[this.data[i].flag])
      }
      this.getGoodsClassData(property1_name)
		},
    getProperty2(data){ //获取第二参数可用数据
      this.axios({
        url:'merchantGoodsStockProperty/'+this.$route.query.id,
        params:{
          property1_name:data,
          key:sessionStorage.shopkey
        }
      }).then(res => {
        console.log('二级',res);
        if(res.data.status == 200){
          let arr = [];
          for(let i of res.data.data){
            arr.push(i.property2_name)
          }
          this.data.property2.text = arr;
        }
      })
    },
    getGoodsClassData(data){//获取商品库存
      this.axios({
        url:'merchantGoodsStock/'+this.$route.query.id,
        params:{
          key:sessionStorage.shopkey,
          property1_name:data[0],
          property2_name:data[1]
        }
      }).then(res => {
        if(res.data.status == 200){
          this.stock_id = res.data.data.id;
          this.imgUrl = res.data.data.pic_url;
          this.price = res.data.data.price;
          this.sales = res.data.data.number;
          this.weight = res.data.data.weight;
        }
      })
    },
		add(){  //添加商品数量
			this.num++;
		},
		subtract(){ //减少购买数量
			if(this.num == 1){
				return false;
			}
			this.num--;
		},
    /**
     * 添加购物车
     * 
     * goods_id: 商品id
     * price: 商品单价
     * number: 商品数量
     * total_price: 订单价格
     * property1_name: 分类名称
     * property2_name: 分类名称
     * pic_url: 图片地址
     * stock_id: 商品分类id
    */
    addShopCart(){
      
      if(this.sales == 0){
        Toast({
          message:"已售罄,请联系商家补充货物",
          duration:1000
        })
        return false;
      }
      let data = {
        goods_id:this.$route.query.id,
        price:this.price,
        number:this.num,
        property1_name: this.data.property1.text[this.data.property1.flag],
        property2_name: this.data.property2.text[this.data.property2.flag],
        total_price:this.price * this.num,
        pic_url:this.imgUrl,
        stock_id:this.stock_id
      }
      this.axios({
        url:'shopCart',
        method:'post',
        data:this.qs.stringify(data)
      }).then(res=>{
        console.log(res);
        if(res.data.status == 200){
          Toast({
            message:"添加成功,您的宝贝在购物车等你呦",
            duration:1000
          })
        }else if (res.data.status == 1001) {
          this.$emit('login','nolog')
        }else{
          Toast(res.data.message)
        }
        
        this.layerFlag = false;
      })
    },
    /**
     * 购买商品,直接生成订单
     * 
     * goods_id: 商品id
     * price: 商品单价
     * number: 商品数量 
     * total_price: 订单价格
     * property1_name: 分类名称
     * property2_name: 分类名称
     * pic_url: 图片地址
     * goods_name: 商品名称
     * stock_id: 商品分类id
    */
    buyGoods(){
      if(sessionStorage.jwt == undefined || sessionStorage.jwt == 'undefined' || sessionStorage.jwt.length == 0){
        this.$emit('login','nolog');
        this.layerFlag = false;
        return false;
      }
      if(this.sales == 0){
        Toast({
          message:"已售罄,请联系商家补充货物",
          duration:1000
        })
        return false;
      }
      let data = [{
        goods_id:this.$route.query.id,
        price:this.price,
        number:this.num,
        total_price:this.price * this.num,
        property1_name: this.data.property1.text[this.data.property1.flag],
        property2_name: this.data.property2.text[this.data.property2.flag],
        pic_url:this.imgUrl,
        goods_name:this.goodsname,
        stock_id:this.stock_id
      }];
      
      sessionStorage.data = JSON.stringify(data);//将即商品参数存放到session里,跳转页面后自动读取
			this.$router.replace({name:'createOrder',query:{price:this.price * this.num}})//将总价传到订单中
    }
	}
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.detail-footer{
	position:fixed;
	bottom:0vw;
	display:flex;
	z-index:5;
	align-items:center;
	border-top:1px solid #eee;
	width:98vw;
	height:12vw;
	background:#fff;
  padding:0vw 1vw;
}

.detail-go{
	display:flex;
	flex-flow:column wrap;
	align-items:center;
	margin:0vw 1vw;
	width:7vw;
	font-size:3vw;
}
.detail-go-img{
	width:5vw;
	height:5vw;
}
.detail-operation{
	display:flex;
	justify-content:center;
	align-items:center;
	flex:1;
	height:9vw;
  padding:0vw 1vw;
	font-size:3vw;
	color:#fff;
	overflow:hidden;
}
.detail-cart{
	flex:1;
	height:100%;
	display:flex;
	justify-content:center;
	align-items:center;
	background:linear-gradient(to right,#ffc802,#fd8b04);
  border-top-left-radius:9vw;
  border-bottom-left-radius:9vw;
}
.detail-buy{
	flex:1;
	height:100%;
	display:flex;
	justify-content:center;
	align-items:center;
	background:linear-gradient(to right,#ff7301,#ff4b03);
  border-top-right-radius:9vw;
  border-bottom-right-radius:9vw;
}


.detail-layer-content{
	display:flex;
	flex-flow:row nowrap;
	height:21vh;
}
.detail-layer-img{
	width:20vh;
	height:20vh;
	border-radius:1vw;
}
.detail-layer-informaition{
	display:flex;
	flex-flow:column wrap;
	justify-content:center;
	align-items:flex-start;
  flex:1;
}
.detail-layer-price{
	color:red;
	margin:2vw;
	font-size:5vw;
}
.detail-layer-price:before{
  content:'￥';
  font-size:4vw;
}
.detail-layer-sales{
	margin:2vw;
	font-size:3.2vw;
	color:#999;
}
.detail-layer-choose{
	margin:2vw;
	font-size:4vw;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2;
}
.detail-layer-choose-item{
  margin:0vw 1vw 0vw 0vw;
}
.detail-layer-classification{
	height:52vh;
	background:#fff;
	overflow:hidden;
}
.detail-layer-classification-scroll{
	overflow:scroll;
	overflow-x:hidden;
	padding-right:4vw;
	width:98vw;
}
.detail-layer-classification-item{
	margin-bottom:0vw;
}
.detail-layer-classification-title{
	font-size:4vw;
	border-bottom:1px solid #eee;
	
}
.detail-layer-classification-list{
	font-size:3.5vw;
	padding:1vw 2vw;
	border-radius: 6vw 6vw;
	margin:2vw 1vw;
	background:#eee;
	overflow:hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
.select{
  color:#ff4b03;
  border:0.2vw solid #ff4b03;
  background:none;
}
.detail-layer-operation{
	padding:2vw;
	font-size:3.5vw;
	border-top:1px solid #eee;
}
.detail-layer-operation-item{
	margin-right:6vw;
  width:20vw;
  justify-content:space-between;
  border-top:1px solid #888;
  border-bottom:1px solid #888;
}
.operation-btn{
	width:5vw;
	margin:0vw 0vw;
  border-right:1px solid #888;
  border-left:1px solid #888;
  display:flex;
  justify-content:center;
  align-items:center;
}
.detail-layer-btn-item{
	border-top:1px solid #eee;
	height:5vh;
	display:flex;
	justify-content:center;
	align-items:center;
  color:#fff;
  padding:1.5vw 0;
}
.layer-buttom-btn{
  
}
.close{
  position:absolute;
  width:4vw;
  height:4vw;
  right:2vw;
  top:2vw;
}


</style>



