<template>
  <div class="applicationForDrawback">
    <headhistory :data='title'></headhistory>
    
    <!--商品展示-->
    <showGood :goods='goodsData'></showGood>
    
    <!--退款类型-->
    <div class='flex-row-between'>
      <h5>退款类型</h5>
      <select v-model='type'>
        <option v-if='after_type == 1' value='2'>仅退款</option>
        <option v-if='after_type != 1' value='1'>退货退款</option>
      </select>
    </div>
    
   
    
    <!--退款理由-->
		<div class='applicationForDrawback-content'>
      <h5>请填写退款理由</h5>
      <textarea v-model='text' placeholder='请描述申请售后服务的具体原因,文字,图片至少填一项,最多可上传5张图片'></textarea>
      
      <div class='applicationForDrawback-content-item'>
        <div v-if='imgs.length < 5' class='applicationForDrawback-content-item-choose' @click='addimg'>
          <img src='/public/image/comments/picture.png'>
        </div>
        <div class='applicationForDrawback-content-img-item' v-for='(item,index) in imgs'>
          <img :src='item' class='applicationForDrawback-content-img'>
          <img src='/public/image/comments/del.png' class='pplicationForDrawback-content-del' @click.stop='subimg(index)'>
        </div>
      </div>
    </div>
    
    <!--底部-->
    <div class='footer'>
      <div class='footer-item' @click.stop='submit'>
        提交
      </div>
    </div>
    
    <login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
import showGood from './showGood' 
import login from'../login/login'
import { Toast } from 'mint-ui'
export default {
  name: 'applicationForDrawback',
  data () {
    return {
      title:'申请售后',
      goodsData:'',
      courierOrder:'',
			type:'',
      text:'',
      imgs:[],
      order_sn:'',
      after_type:'',
      showFlag:false
    }
  },
	components:{
		headhistory,
		showGood,
    login
	},
  created(){
    if(sessionStorage.jwt == undefined || sessionStorage.jwt == 'undefined' || sessionStorage.jwt.length == 0){
      this.login();
      return false
    }
		this.getData();
	},
	methods:{
		addimg(){//选择图片
      wx.chooseImage({
        count: 5 - this.imgs.length, // 默认9
        sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
        sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
        success:res => {
          var localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
          this.imgs.push(...localIds)
        }
      });
    },
    subimg(index){
      this.imgs.splice(index,1)
    },
    getData(){//获取数据接口
      this.axios({
        url:'shopOrder/'+this.$route.query.order_sn
      }).then(res => {
        if(res.data.status == 200){
          this.order_sn = res.data.data.order_sn;
          this.goodsData = res.data.data.order;
          this.after_type = res.data.data.status;
          this.type = this.after_type == 1 ? 2 : 1;
        }
      })
    },
    async uploadImg(images){
      return await new Promise((resolve,reject) =>{
				wx.uploadImage({
					localId:images,
					isShowProgressTips: 1,
					success:function(res){
						resolve(res.serverId);
					},
					fail:function(res){
					}
				})
			});
    },
    async submit(){//提交数据接口
      if(this.type == ''){
        Toast('请选择退款类型');
        return false;
      }
      if(this.text == ''){
        Toast('请填写退款理由');
        return false;
      }
      
      let img = [];
      
      for(let i of this.imgs){
        img.push(await this.uploadImg(i))
      }
      
      let data = {
        order_sn:this.order_sn, //this.$route.query.id 快递单号,
        after_imgs:JSON.stringify(img),//图片id,问为什么是id,自己去查微信文档
        after_remark:this.text,//退款理由
        after_type:this.type,
        type:1
      }
      this.axios({
        url:'shopOrderAfter',
        method:'put',
        data:this.qs.stringify(data)
      }).then(res => {
        console.log(res);
        if(res.data.status == 200){
          this.$router.replace({name:'returnOrder'});
        }else{
          alert(res.data.status)
        }
      })
    },
    login (data) {
      this.showFlag = !this.showFlag;
    }
	}
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.applicationForDrawback{
  width:96vw;
  overflow:hidden;
  background:#f2f2f2;
  padding:14vw 2vw 14vw 2vw;
  
  .flex-row-between{ //选择售后类型
    padding:4vw 2vw;
    background:#fff;
    width:92vw;
    border-radius:2vw;
    margin-bottom:2vw;
    margin-top:2vw;
    select{
      appearance: none;
      border: none;
      background:none;
      direction: rtl;
    }
    
    
  }
  
  .applicationForDrawback-content{ //用户填写区域
    background:#fff;
    width:92vw;
    border-radius:2vw;
    padding:4vw 2vw;
    margin-top:2vw;
    
    textarea{
      width:88vw;
      height:25vw;
      border:none;
      background:#eee;
      margin-top:1vw;
      border-radius:2vw;
      padding:2vw;
      resize:none;
      font-size:3vw;
    }
    
    .applicationForDrawback-content-item{ //上传图片
      display:flex;
      flex-flow:row wrap;
      justify-content:space-between;
      align-items:center;
      &:after{
        content:'';
        width:29vw;
      }
      
      .applicationForDrawback-content-item-choose{
        width:29vw;
        height:29vw;
        display:flex;
        justify-content:center;
        align-items:center;
        margin-top:2.5vw;
        box-sizing:border-box;
        border:1px solid #eee;
        
        img{
          width:20vw;
          height:20vw;
        }
        
      }
      
      .applicationForDrawback-content-img-item{
        width:29vw;
        height:29vw;
        margin-top:2.5vw;
        position:relative;
        box-sizing:border-box;
        border:1px solid #eee;
        
        .applicationForDrawback-content-img{ //图片
          width:29vw;
          height:29vw;
        }
        
        .pplicationForDrawback-content-del{
          position:absolute;
          width:5vw;
          height:5vw;
          right:0vw;
          top:0vw;
        }
        
      }
      
      
      
      
    }
    
  }
  
  
  .footer{ //底部
    justify-content:flex-end;
    
    .footer-item{  //按钮
      flex:none;
      height:100%;
      width:25vw;
      font-size:3.5vw;
      border-left:1px solid red;
      border-right:1px solid red;
      background:linear-gradient(to right,#ff7301,#ff4b03);
      color:#fff;
    }
    
  }
  
}
</style>



