<template>
  <div class="payOk">
    <headhistory :data='title' v-if='true'></headhistory>
    <div class='content'>
      <div class='title'>
        <img src='/public/image/payOk/ok.png' class='title-log'>
        <div>付款成功</div>
      </div>
      <div class='price'>￥{{price}}</div>
      <div class='orderitem' @click.stop.once='go'>查看订单详情</div>
    </div>
		<recommended class='recommended'></recommended>
		<banner class='banner'></banner>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
import recommended from './recommended' 
import banner from './banner' 
export default {
  name: 'payOk',
  data () {
    return {
      title:'支付结果',
			price:'1029.00',
      order_sn:''
    }
  },
	components:{
		headhistory,
    recommended,
    banner
	},
	methods:{
		go(){
      this.$router.push({name:'orderItem',query:{id:this.order_sn}})
    }
	},
	created(){
    
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.payOk{
  padding-top:12vw;
  width:100vw;
  overflow:hidden;
  background:#fff;
  .content{
    display:flex;
    flex-flow:column wrap;
    justify-content: flex-start;
    width:100vw;
    margin-top:5vh;
    align-items: center;
    
    .title{ //标题
      color:#8ab259;
      font-size:8vw;
      font-weight:700;
      display:flex;
      justify-content:center;
      align-items:center;
      
      .title-log{ //标题图片
        width:9vw;
        height:9vw;
        margin-right:2vw;
      }
    }
    
    .price{ //金额
      font-size:6vw;
      margin:4vw 0vw;
    }
    
    .orderitem{ //订单详情按钮
      background:#ff3636;
      color:#fff;
      width:40vw;
      display:flex;
      justify-content:center;
      align-items:center;
      height:9vw;
      font-size:4vw;
      border-radius:1vw;
    }
  }
  
  .recommended{
    margin-top:4vh;
  }
  
  .banner{
    margin-top:4vh;
  }
}

</style>



