<template>
  <div class="store">
		<div class='store-item' @click.stop='goIndex'>
			<img :src='data.pic_url' class='store-img'>
      <div class='store-content'>
        <div class='store-name'>{{data.name}}</div>
        <div class='store-right'>
          <div class='store-right-text'>进店逛逛</div>
          <img src='/public/image/input/go.png' class='store-go'>
        </div>
      </div>
		</div>
    <div class='flex-row-between'>
      <div class='info-item'>
        <img class='info-img' src='/public/image/goodsDetails/content/yes.png'>
        <div>微信支付</div>
      </div>
      <div class='info-item'>
        <img class='info-img' src='/public/image/goodsDetails/content/yes.png'>
        <div>官方认证</div>
      </div>
    </div>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  name: 'goodsDetail-store',
  data () {
    return {
			data:[]
    }
  },
	methods:{
    /**
     * 获取商家信息
    */
		getData:function(){
			this.axios({
				url:'ShopAppInfo',
        params:{
          key:sessionStorage.shopkey
        }
			}).then((res)=>{
        if(res.data.status == 200){
          this.data = res.data.data;
        }
			})
		},
    /**
     * 跳转到首页
    */
    goIndex () {
      this.$router.push({name:'index',query:{key:sessionStorage.shopkey}})
    }
	},
	created(){
		this.getData();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.store{
	background:#fff;
	margin-top:1vw;
	margin-bottom:1vw;
	display:flex;
	flex-flow:row wrap;
	justify-content:flex-start;
	align-items:center;
	padding:2vw;
  padding-left:4vw;
}
.store-item{
	display:flex;
	width:96vw;
	justify-content:flex-start;
	align-items:center;
  padding-bottom:1vw;
  .store-img{
    width:6vw;
    height:6vw;
    border-radius:1vw;
    padding:2vw 0;
  }
  .store-content{
    flex:1;
    display:flex;
    justify-content:space-between;
    align-items:center;
    .store-name{
      margin:0vw 1vw;
      font-size:3.5vw;
    }
    .store-right{
      display: flex;
      justify-content:center;
      align-items:center;
      .store-right-text{
        font-size:3vw;
      }
      .store-go{
        width:4vw;
        height:4vw;
        margin: 0vw 1vw;
      }
    }
  }
}
.flex-row-between{
  padding-top:1vw;
  border-top:1px solid #eee;
  flex:1;
	font-size:3.5vw;
	color:#999;
  display:flex;
  justify-content:flex-start;
  align-items:center;
  .info-item{
    display:flex;
    justify-content:flex-start;
    align-items:center;
    padding:2vw 0;
    margin-right:2vw;
    .info-img{
      width:3.5vw;
      height:3.5vw;
      margin-right:0.5vw;
    }
  }
}



.store-btn-white{
	background:linear-gradient(to bottom,#22dfc6,#31cfcd);
	color:#fff;
}

.store-score{
	font-size:3.2vw;
	color:red;
}
</style>



