<template>
  <transition name='fade'>
    <div v-if='showFlag' class="share" @touchmove.stop @click.stop='hidden'>
      <img class='share-img' src='/public/image/goodsDetails/share/yindao.png'>
      <div class='share-text'>
        请点击右上角<br/>
        通过[发送给朋友]<br/>
        邀请好友购买
      </div>
    </div>
  </transition>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  name: 'goodsDetail-share',
  props:{
    data:Object
  },
  data () {
    return {
			showFlag:false,
      res:''
    }
  },
  created () {
    this.toCircle();
    this.toFriend();
  },
	methods:{
    /**
     * 分享给朋友
    */
		toFriend () {
      console.log('分享给朋友')
			wx.updateAppMessageShareData({
        title: this.data.name, // 分享标题
        desc: this.data.name, // 分享描述
        link: location.href, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: this.data.pic_urls[0], // 分享图标
        success: function (res) {// 用户点击了分享后执行的回调函数
          this.showFlag = false;
        }
      });
		},
    /**
     * 分享到朋友圈
    */
    toCircle () {
      console.log('分享给朋友圈')
      wx.updateTimelineShareData({
        title: this.data.name, // 分享标题
        link: location.href, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: this.data.pic_urls[0], // 分享图标
        success: function (res) {// 用户点击了分享后执行的回调函数
          this.showFlag = false;
        }
      })
    },
    show () {
      this.showFlag = true;
    },
    hidden () {
      this.showFlag = false;
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.share{
  position:fixed;
  top:0;
  height:0;
  width:100vw;
  height:100vh;
  z-index:1000;
  background:rgba(0,0,0,0.4);
  .share-img{
    width:20vw;
    position:absolute;
    right:0;
    top:0;
  }
  .share-text{
    position:absolute;
    left:10vw;
    top:20vw;
    color:#fff;
    font-size:8vw;
  }
}
.fade-enter-active, .fade-leave-active{
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to{
  opacity:0;
}
</style>



