<template>
  <div class="orderItem">
    <headhistory :data='title' v-if='true'></headhistory>
    <orderItemAddress :address='addressData'></orderItemAddress>
    <orderItemGoods :goods='goodsData'></orderItemGoods>
    <orderItemOrder :order='orderItem'></orderItemOrder>
    <orderItemFooter :status='status' :order_sn='order_sn' @getData='getData' @gologInformation='gologInformation'></orderItemFooter>
    <login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
import orderItemAddress from './orderItemAddress'
import orderItemGoods from './orderItemGoods'
import orderItemOrder from './orderItemOrder'
import orderItemFooter from './orderItemFooter'
import login from'../login/login'
export default {
  name: 'orderItem',
  data () {
    return {
      title:'订单详情',
			addressData:'收货地址',
      goodsData:'商品信息',
      orderItem:'订单信息',
      logisticsInformation:'物流信息',
      order_sn:this.$route.query.order_sn,
      status:'',
      showFlag:false
    }
  },
	components:{
		headhistory,
		orderItemAddress,
		orderItemGoods,
		orderItemOrder,
		orderItemFooter,
    login
	},
	created(){
    if(sessionStorage.jwt == undefined || sessionStorage.jwt == 'undefined' || sessionStorage.jwt.length == 0){
      this.login();
      return false
    }
		this.getData();
	},
	methods:{
    /**
     * 获取数据
     *
     * name: 收货人姓名
     * phone: 手机电话
     * address: 收货地址
     *
     * status 订单状态
     * data: 商品数据
     * orderAllPrice: 商品总价
     * express_price: 运费
     * voucher: 红包价格
     * payment_money: 实付款金额
     * total_price: 订单总价
     *
     * pay_time: 付款时间
     * create_time: 创建时间
     * send_out_time: 发货时间
     * order_sn: 订单编号
     * transaction_id: 微信交易号
    */
		getData(){
      this.axios({
        url:'shopOrder/'+this.$route.query.order_sn
      }).then(res => {
        this.addressData = {//收货地址
          name:res.data.data.name,
          phone:res.data.data.phone,
          address:res.data.data.address,
        }
        this.status = res.data.data.status; //订单状态
        let goods = {
          data:res.data.data.order,
          orderAllPrice:(res.data.data.total_price*1 - res.data.data.express_price*1).toFixed(2), 
          express_price:res.data.data.express_price, 
          voucher:parseFloat(res.data.data.voucher_price.price).toFixed(2), 
          payment_money:res.data.data.payment_money, 
          total_price:parseFloat(res.data.data.total_price*1 - res.data.data.voucher_price.price*1).toFixed(2) 
        }
        this.goodsData = goods;
        let order = { //订单
          pay_time:res.data.data.weixinOrder.pay_time == '' ? '未付款':res.data.data.weixinOrder.pay_time, 
          create_time:res.data.data.create_time, 
          send_out_time:res.data.data.send_out_time != '' ? res.data.data.send_out_time : '未发货',
          order_sn:res.data.data.order_sn,
          transaction_id:res.data.data.weixinOrder.transaction_id != null ? res.data.data.weixinOrder.transaction_id : '未付款'
        }
        this.orderItem = order;
        
        this.logisticsInformation = res.data.data.id //订单id
      })
    },
    /**
     * 跳转到
    */
    gologInformation(){
      this.$router.push({name:'logisticsInformation',query:{id:this.logisticsInformation}});
    },
    login (data) {
      this.showFlag = !this.showFlag;
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.orderItem{
  padding-top:12vw;
  background:#eee;
}

</style>



