<!--订单详情的地址-->
<template>
  <div class="orderItemFooter">
    <div v-if='status == 3 || status == 6' class='btn' @click.stop='checkLogistics(order_sn)'>查看物流</div>
    <div class='btn' v-if='status == 3' @click='getGoods(order_sn)'>确认收货</div>
    <div v-if='status != 0 && status !=7 && status != 2' class='btn' @click.stop='returnGoods(order_sn)'>退货</div>
    <div v-if='status == 6 || status == 7 || status == 2' class='btn' @click.stop='delOrder(order_sn)'>删除订单</div>
    <div v-if='status == 0' class='btn' @click.stop='unpay(order_sn)'>取消订单</div>
    <div v-if='status == 6' class='btn' @click.stop='evaluation(order_sn)'>评价</div>
    <div class='btn' v-if='status == 0' @click='gopay(order_sn)'>付款</div>
    
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { MessageBox } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'orderItemFooter',
  props:{
    order_sn:String,
    status:String
  },
  data () {
    return {
			
    }
  },
  created(){
		
	},
	components:{
		
	},
	methods:{
		go(){
      this.$emit('gologInformation','')
    },
    /**
     * 支付
    */
    gopay(order_sn){
      Indicator.open('Loading...');
      let data = {
        type:1
      }
      this.axios({
        url:`shopGoPay/${order_sn}`,
        method:'post',
        data:this.qs.stringify(data)
      }).then(res => {
        Indicator.close();
        if(res.data.status == 200){
          wx.chooseWXPay({
            timestamp: res.data.data.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
            nonceStr: res.data.data.nonceStr, // 支付签名随机串，不长于 32 位
            package: res.data.data.package, // 统一支付接口返回的prepay_id参数值
            signType: res.data.data.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
            paySign: res.data.data.paySign, // 支付签名
            success: res => {
              this.$router.replace({name:'order'})
            }
          })
        }else{
          Toast(res.data.message);
        }
      })
    },
    /**
     * 查询物流
    */
    checkLogistics(order_sn){
      this.$router.push({name:"logisticsInformation",query:{order_sn:order_sn}})
    },
    /**
     * 退货跳转
    */
    returnGoods(order_sn){ 
      this.$router.replace({name:'applicationForDrawback',query:{order_sn:order_sn}})
    },
    /**
     * 确认收货
    */
    getGoods(order_sn){
      MessageBox.confirm('确定已收货?').then(action => {
        let data = {
          order_sn: order_sn
        }
        this.axios({
          url:'shopOrderGoods',
          method:'put',
          data:this.qs.stringify(data)
        }).then(res => {
          if(res.data.status == 200){
            Toast('收货完毕');
            this.$emit('getData','aaa')
          }else{
            Toast(res.data.message);
          }
          
        })
      });
    },
    /**
     * 取消订单
    */
    unpay(order_sn){
      MessageBox.confirm('是否取消订单?').then(action => {
        this.axios({
          url:`shopUnOrder/${order_sn}`,
          method:'put'
        }).then(res => {
          if(res.data.status == 200){
            this.$emit('getData','aaa')
          }else{
            Toast(res.data.message);
          }
        })
      });
    },
    /**
     * 删除订单
    */
    delOrder(order_sn){
      MessageBox.confirm('是否删除订单?').then(action => {
        Indicator.open('Loading...');
        this.axios({
          url:`shopOrder/${order_sn}`,
          method:'delete'
        }).then(res => {
          console.log(res);
          Indicator.close();
          if(res.data.status == 200){
            Toast('删除订单成功');
            this.$router.replace({name:"order"})
          }else{
            Toast(res.data.message);
          }
        })
      });
    }
	},
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.orderItemFooter{
  position:fixed;
  bottom:0vw;
  left:0vw;
  width:100vw;
  display:flex;
  justify-content:flex-end;
  align-items:center;
  border-top:1px solid #eee;
  background:#fff;
  
  .btn{
    padding:2vw 4vw;
    border-radius:5vw;
    font-size:3vw;
    margin:2vw;
    border:1px solid #bbb;
    color:#555;
  }
  
}

</style>



