<template>
  <div v-if='!data' class='message'>
		<img :src='imgUrl' class='message-logo'>
		<transition name="slide-fade">
			<div v-if='flag'>
				<div class='message-content' v-for='(item,index) in data' @click.stop='go(item.url)'>
					<span class='message-label'>{{item.label}}</span>
					<span class='message-text'>{{item.text}}</span>
				</div>
			</div>
		</transition>
		
	</div>
</template>

<script>
export default {
  name: 'aaa',
  data () {
    return {
			imgUrl:'/public/image/index/message/quanzi.png',
			data:[],
			pages:0,
			flag:true
    }
  },
	methods:{
		getList:function(index){
			this.axios({
				method:'get',
				url:'message',
				params:{
					id:index,
          key:sessionStorgae.shopkey
				}
			}).then((res)=>{
				if(res.data.status==200){
					this.flag = true;
					this.data = res.data.data;
					this.pages = res.data.pages
				}
			})
		},
		go:function(data){
			this.$router.push({name:data})
		}
	},
	created:function(){
		let i = 1;
		this.getList(i);
		i++;
		this.dingshiqi = window.setInterval(()=>{/*计时获取数据*/
			if(i == this.pages+1){
				i = 1;
			}
			this.flag = false;
			this.getList(i);
			i++
		},60000)
		console.log('created',this.dingshiqi)
	},
	destroyed:function(){
		window.clearInterval(this.dingshiqi);
		console.log('end',this.dingshiqi)
	}
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.message{
	display: flex;
	justify-content: flex-start;
	align-items: center;
	background: linear-gradient(#fff, #f0ecea);
	padding: 2vw 0;
}
.message-content{
	display: flex;
	justify-content: flex-start;
	align-items: center;
	flex: row wrap;
	margin: 1vw 0;
}
.message-logo{
	width: 10vw;
	margin: 0 2vw;
}
.message-label{
	width: 15vw;
	height:5vw;
	line-height:5vw;
	color: #fbcc71;
	border: 1px solid #fbcc71;
	font-size: 2vw;
	text-align: center;
	margin: 0 2vw;
}
.message-text{
	width:65vw;
	font-size: 3vw;
	color: #555;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}
.slide-fade-enter-active {
  transition: all .5s ease;
}
.slide-fade-leave-active {
  transition: all 1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to{
  transform: translateY(6vw);
  opacity: 0;
}
</style>



