<template>
  <div class="cart">
		<div class='cart-item'>
			<div v-for='(item,index) in cartdata' class='rongqi'>
				<div class='flex-row-wrap align-top'>
					<div class='flex-row-wrap align-top'>
						<img :src='item.pic_url' class='cart-store-logo'>
						<div class=''>
							<div class='cartInformaition'>
								<div class='cartInformaition-text'>{{item.goods_name}}</div>
								<div class='cartInformaition-type' v-if='item.property1_name'>{{item | property}}</div>
							</div>
							<div class='flex-row-between'>
								<div class='cartitem-price'>￥{{item.price}}</div>
								<div class='flex-row-wrap'>
									<div class='cart-nums'>x{{item.number}}</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'cart',
	props:['cartdata'],
  data () {
    return {
			goods:''
    }
  },
	components:{
		
	},
  filters:{
    property(val){
      let data = val.property1_name + ',' + val.property2_name;
      return data;
    }
  },
  created(){
		
	},
	methods:{
		
	}
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.cart{
	background:#fff;
  padding: 0vw 2vw;
  border-top:1px solid #eee;
}
.cart-item{
	padding-top:2vw;
}
.rongqi{
	border:1px solid #fff;
	padding:3vw 2vw 3vw 2vw;
	background:#fff;
  border-bottom:1px solid #eee;
}
.checkbox{
	margin-top:8vw;
}
.cart-store-logo{
	width:20vw;
	height:20vw;
	border-radius:1vw;
	margin:0vw 2vw;
}
.cartInformaition{
	font-size:3.2vw;
	width:55vw;
}
.cartInformaition-text{
	margin-bottom:2vw;
	font-size:3.5vw;
	letter-spacing:1px;
}
.cartInformaition-type{
	background:#f9f9f9;
	border-radius:1vw;
	font-size:3.3vw;
	color:#888;
}
.flex-row-between{
	margin-top:5vw;
}
.cartitem-price{
	color:red;
}
.cart-btn{
	width:5vw;
	height:5vw;
	font-size:3.2vw;
	border:1px solid #eee;
	display:flex;
	justify-content:center;
	align-items:center;
}
.cart-nums{
	width:10vw;
	height:5vw;
	font-size:3.2vw;
	text-align:center;
	display:flex;
	justify-content:center;
	align-items:center;
}
</style>



