<!--订单详情的地址-->
<template>
  <div class="orderItemAddress">
    <div v-if='address.name' class='orderItemAddress-firstname'>{{address.name[0]}}</div>
    <div class='orderItemAddress-content'>
      <div class='orderItemAddress-content-item'>
        <div class='orderItemAddress-content-name'>{{address.name}}</div>
        <div class='orderItemAddress-content-phone'>{{address.phone}}</div>
      </div>
      <div class='orderItemAddress-content-address'>
        {{address.address}}
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'orderItemAddress',
  props:['address'],
  data () {
    return {
			
    }
  },
  created(){
		
	},
	components:{
		
	},
	methods:{
		
	},
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.orderItemAddress{
  display:flex;
  justify-content:flex-start;
  align-items:center;
  flex-flow:row nowrap;
  background:#fff;
  padding:0vw 2vw;
  .orderItemAddress-firstname{ //收货地址图片
    width:10vw;
    height:10vw;
    text-align:center;
    border-radius:50%;
    background:red;
    display:flex;
    align-items:center;
    justify-content:center;
    color:#fff;
    background-color:#bec0c2;
  }
  
  .orderItemAddress-content{ //地址详情
    flex:1;
    margin:2vw;
    
    .orderItemAddress-content-item{ //地址详情
      display:flex;
      flex-flow: row wrap;
      justify-content:flex-start;
      align-items:center;
      margin:2vw 0vw;
      
      .orderItemAddress-content-name{ //收货人姓名
        font-size:4vw;
        margin-right:2vw;
      }
      
      .orderItemAddress-content-phone{ //收货人手机号
        color:#aaa;
        font-size:4vw;
      }
      
    }
    
    .orderItemAddress-content-address{ //收货人地址
      margin:2vw 0vw;
      font-size:4vw;
      font-weight:500;
    }
    
  }
  
}

</style>



