<template>
  <div v-if='showFlag' class="login" @touchmove.prevent>
    <div class='login-item'>
      <div class='login-title'>提示</div>
      <div class='login-content'>未登录无法使用此功能,是否登录?</div>
      <div class='login-btn'>
        <div class='login-btn-item' @click.stop='nologin'>取消</div>
        <div class='login-btn-item' @click.stop='tologin'>确定</div>
      </div>
    </div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { MessageBox  } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'login',
  props:{
    showFlag: {
      type: Boolean,
      required: true,
      default: false
    }
  },
  data () {
    return {
      
    }
  },
	methods:{
		tologin () {
      sessionStorage.setItem('url',this.$route.fullPath)
      location = `https://api.juanpao.com/shopLogin?key=${sessionStorage.shopkey}&type=wechat`
    },
    nologin () {
      this.$emit('close','close')
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
*{
	margin:0;
	padding:0;
}

.login{
  position:fixed;
  width:100vw;
  height:100vh;
  background:rgba(0,0,0,0.2);
  z-index:999;
  top:0;
  left:0;
  display:flex;
  justify-content:center;
  align-items:center;
  .login-item{
    width:85vw;
    background:#fff;
    .login-title{
      text-align:center;
      padding-top:15px;
      text-align: center;
      padding-left: 0;
      margin-bottom: 0;
      font-size: 16px;
      font-weight: 700;
      color: #333;
    }
    .login-content{
      padding: 10px 20px 15px;
      border-bottom: 1px solid #ddd;
      min-height: 36px;
      position: relative;
      color: #999;
      margin: 0;
      text-align: center;
      line-height: 36px;
    }
    .login-btn{
      display:flex;
      justify-content:space-around;
      align-items:center;
      border-top:1px solid #eee;
      height: 40px;
      line-height: 40px;
      .login-btn-item{
        border-right: 1px solid #ddd;
        flex:1;
        text-align:center;
      }
    }
  }
}
</style>



