<template>
	<div class='goods'>
    <div @click='go(item.id)' class='goods-item' v-for='(item,index) in data'>
      <div class='goods-item-flex'>
        <img :src='item.stock.pic_url' class='goods-img'>
        <div class='goods-text' style='-webkit-box-orient:vertical; -webkit-line-clamp:2; '>{{item.name}}</div>
        <div class='goods-content'>
          <div class='goods-price'>￥<span>{{item.price}}</span></div>
          <div class='goods-sold'>{{item.sold}}人付款</div>
        </div>
      </div>
    </div>
	</div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: 'goods',
  data () {
    return {
			data:[]
    }
  },
	methods:{
		go:function(data){
      this.$router.push({name:'goodsDetails',query:{id:data,key:this.$route.query.key}})
		},
    getGoodsList:function(){
			this.axios({
				url:'shopIsTopGoods',
				method:'get',
        params:{
          key:sessionStorage.shopkey
        }
			}).then((res)=>{
				if(res.data.status == 200){
					this.data = res.data.data;
				}else{
					//alert("数据获取失败")
          console.log('商品数据获取失败',res.data);
				}
			})
		}
	},
	created:function(){
		this.getGoodsList();
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.goods{
	overflow:hidden;
  padding:2vw 2vw 0 2vw;
  display:flex;
  justify-content:space-between;
  align-items:flex-start;
  flex-flow:row wrap;
}

.goods-item{
	display:inline-block;
	margin:0.5vw;
  width:46vw;
}
.goods-item-flex{
	display:flex;
	flex-flow: column wrap;
	align-items:flex-start;
  justify-content:flex-start;
  background:#fff;
  margin-bottom:2vw;
  box-shadow:0vw 1vw 1vw #ddd;
  padding-bottom:3vw;
}
.goods-img{
	width:46vw;
	height:46vw;
}
.goods-text{
	font-size:3.5vw;
  overflow:hidden; 
  text-overflow:clip;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2; 
  margin:2vw 2vw;
  height: 9vw;
}
.goods-content{
  width:96%;
  display:flex;
  flex-flow:row wrap;
  align-items:center;
  justify-content:space-between;
  .goods-price{
    font-size:2vw;
    color:red;
    padding:0vw 1vw;
    span{
      font-size:4.5vw;
    }
  }
  .goods-sold{
    color:#aaa;
    font-size:3vw;
  }
}

</style>



