<template>
  <div class="order">
		<headhistory :data='title'></headhistory>
		<orderHeader @id='show'></orderHeader>
		<orderItem :id='id'></orderItem>
		<login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import headhistory from'../history/headhistory'
import orderHeader from'./orderHeader'
import orderItem from'./orderItem'
import login from'../login/login'
export default {
  name: 'order',
  data () {
    return {
      title:'我的订单',
			id:1,
      showFlag:false
    }
  },
	components:{
		orderHeader,
		orderItem,
		headhistory,
    login
	},
	methods:{
		show(id){
			this.id = id;
		},
    login (data) {
      this.showFlag = !this.showFlag;
    }
	},
	created(){
		if(sessionStorage.jwt == undefined || sessionStorage.jwt == 'undefined' || sessionStorage.jwt.length == 0){
      this.login();
      return false
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



