<template>
  <div class="classificationDetailsItem">
		<!-- 内容页-start -->
		<div class='classifyGoods-item'>
			<div class='classifyGoods-content' v-for='(item,index) in data' :key='index' @click.stop='go(item.id)'>
				<img :src='item.stock.pic_url' class='classifyGoods-img'>
				<div class='classifyGoods-text'  style='-webkit-box-orient:vertical; -webkit-line-clamp:2; '>{{item.name}}</div>
				<div class='classifyGoods-price-sold'>
					<div class='classifyGoods-price'>￥{{item.price}}</div>
					<div class='classifyGoods-sold'>{{item.sold}}已售</div>
				</div>
			</div>
		</div>
		<!-- 内容页-end -->
		
		<!-- 加载动画-start -->
		<div class='loading'>
			<div>已经到最底部啦</div>
		</div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'classificationDetailsItem',
	components:{
	},
  data () {
    return {
      data:[],
      current_page:0,
      pages:1,
      flag:false
    }
  },
	methods:{
		getMenusData:function(id){//获取菜单商品
      Indicator.open('Loading...');
			this.axios({
				url:'shopGoods',
				method:'get',
				params:{
          m_category_id:id,
          current_page:++this.current_page,
          key:this.$route.query.key
				}
			}).then((res)=>{
        console.log(res.data)
        Indicator.close();
				if(res.data.status == 200){
          this.data.push(...res.data.data);
          this.pages = res.data.count;
          this.flag = false;
				}else{
					Toast(res.data.message);
				}
			})
		},
    go(data){
      this.$router.push({name:'goodsDetails',query:{id:data,key:this.$route.query.key}});
    }
	},
	created(){
    let _this = this;
    this.getMenusData(this.$route.query.id);
    window.addEventListener('scroll',function(){
			let scrollY = this.scrollY;
			let i = 1;
			if(document.body.scrollHeight <= scrollY+window.screen.height){//如果(网页高度 小于或等于 滚动条高度 + 浏览器高度)
        if(_this.pages > _this.current_page &&  _this.flag == false){
          _this.getMenusData(_this.$route.query.id);
          console.log(_this.current_page)
        }
        _this.flag = true;
			}
		},false);
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.classificationDetailsItem{
	margin-top:12vw;
}
.classifyGoods-item{
	width:100%;
	display:flex;
	justify-content:flex-start;
	align-items:center;
	flex-flow: row wrap;
	overflow:hidden;
	margin-top:2vw;
}
.classifyGoods-content{
	width:47vw;
	margin:1vw;
  border:1px solid #eee;
	overflow:hidden;
  box-shadow:0px 1px 1px #eee;
  padding-bottom:1vw;
}
.classifyGoods-img{
	width:47vw;
	height:47vw;
	border-radius: 1vw 1vw;
}
.classifyGoods-text{
  margin:1vw 2vw;
	font-size:3vw;
  overflow:hidden; 
  height:9vw;
  text-overflow:clip;
  display:-webkit-box;
}
.classifyGoods-price-sold{
  margin:0vw 2vw;
	display:flex;
	justify-content:space-between;
	align-items:center;
	font-size:2vw;
}
.classifyGoods-price{
	color:red;
}
.classifyGoods-sold{
	color:#999;
}
.loading{
	display:flex;
	justify-content:center;
	align-items:center;
	color:#eee;
	font-size:3vw;
}
</style>



