<template>
  <div class="address">
		<div class='address-title'>
			<img src='/public/image/input/back.png' class='back' @click.stop='hide'>
			<div>{{id=='add' ? "新建收货地址" : "编辑收货地址"}}</div>
			<div @click.stop='submit'>保存</div>
		</div>
		<div class='area'>
			<div class='label'>收货人</div>
			<input type='text' class='informartion' v-model='item.name'>
		</div>
		<div class='area'>
			<div class='label'>手机号码</div>
			<input type='number' class='informartion' v-model='item.phone'>
		</div>
		<div class='area'>
			<div class='label'>所在地区</div>
			<div class='informartion' @click.stop='show'>{{item.province}} {{item.city}} {{item.area}} {{item.street}}</div>
		</div>
		<div class='area'>
			<div class='label'>详细地址</div>
			<textarea type='number' class='informartion' v-model='item.address'></textarea>
		</div>
		<div class='area'>
			<div class='label'>设置默认地址</div>
			<input type='checkbox' class='informartion checkbox' v-model='item.is_default'>
		</div>
		<div class='mask2' v-show='flag' id='mask'></div>
		<div v-if='editflag' class='remove' @click='showRemove'>删除收货地址</div>
		<selectcity v-if='selectFlag' @selectHide='selectHide' @address='address'></selectcity>
  </div>
</template>

<script>
import selectcity from'./selectcity'
import { MessageBox } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'HelloWorld',
	props:['id'],
  data () {
    return {
      selectFlag:false,
			item:{
				name:'',
				phone:'',
				address:'',
				province:'',
				city:'',
				area:'',
				street:'',
				is_default:false
			},
			flag:false,//控制弹出层的显示
			editflag:false
    }
  },
	components:{
		selectcity
	},
	methods:{
		hide(){
			this.$emit('hide','yincang')
		},
		show(){
			this.selectFlag = true;             
		},
		selectHide(data){
			this.selectFlag = false;
		},
		address(data){//地址赋值
			this.item.province = data.province;
			this.item.city = data.city;
			this.item.area = data.area;
			this.item.street = data.street;
		},
		submit(){/*验证*/
			let tosta = document.createElement('div');
			tosta.style.background = '#eee';
			tosta.style.display = 'flex';
			tosta.style.padding = '2vw';
			tosta.style['border-radius'] = '2vw';
			tosta.style['flex-flow'] = 'column wrap';
			tosta.style.width = '60vw';
			if(this.item.name.length == 0){
				let div = document.createElement('div');
				div.innerHTML = '名称未填写';
				tosta.appendChild(div);
				this.flag = true;
			}
			if(this.item.phone.length != 11){
				let div = document.createElement('div');
				div.innerHTML = '手机号错误';
				tosta.appendChild(div);
				this.flag = true;
			}
			if(this.item.address.length == 0){
				let div = document.createElement('div');
				div.innerHTML = '详细地址未填写';
				tosta.appendChild(div);
				this.flag = true;
			}
			if(this.flag != true){
        if(this.id == 'add'){//新增地址
          this.post()
        }else{ //修改地址
          this.put();
        }
			}
      if(this.flag != false){
        document.getElementById('mask').appendChild(tosta);
        let _this = this;
        window.setTimeout(()=>{
          _this.flag = false;   
          document.getElementById('mask').removeChild(tosta)
        },2000)
      }
		},
		post(){//新建地址
			this.axios({
				url:'shopContact',
				data:this.qs.stringify(this.item),
				method:'post'
			}).then((res)=>{
				console.log('提交地址',res);
        if(res.data.status == 200){
          this.hide();
        }
			})
		},
    put(){//修改地址
      this.axios({
				url:`shopContact/${this.id}`,
				data:this.qs.stringify(this.item),
				method:'put'
			}).then((res)=>{
				console.log('修改地址',res);
        if(res.data.status == 200){
          Toast('修改完毕');
          this.hide();
        }
			})
    },
		get(){
			this.axios({
				url:`shopContact/${this.id}`,
				method:'get'
			}).then((res)=>{
				console.log(res.data.data);
				this.item = res.data.data;
			})
			this.editflag = true
		},
		showRemove(){
			MessageBox.confirm('确定删除该信息?').then(action => {
				this.axios({
          url:`shopContact/${this.id}`,
          method:'delete'
        }).then(res =>{
          console.log(res);
          if(res.data.status == 200){
            Toast('删除成功');
            this.hide();
          }else{
            Toast(res.data.message);
          }
        })
			});
		}
	},
	created(){
		this.id == 'add'? console.log('a'): this.get();
	},
	watch:{
		'item.is_default':{
      handler(val,oldVal){
        console.log(val);
        if(val == false){
          this.item.is_default = 0
        }else if(val == true){
          this.item.is_default = 1
        }else if(val == 1){
          this.item.is_default = true
        }else if(val == 0){
          this.item.is_default = false;
        }
        
      },
      deep:true
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.address {
  background: #fff;
  padding-bottom: 2vw;
}
input {
  outline: none;
  border: 0px solid red;
  background: none;
}
.address-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #eee;
  padding: 3vw 2vw;
}
.address-save {
  margin-right: 2vw;
}
.back {
  width: 5vw;
  height: 5vw;
}
.area {
  padding: 4vw 2vw;
  display: flex;
  flex-flow: row wrap;
  border: 1px solid #eee;
}
.label {
  width: 20vw;
  font-size: 3.3vw;
  font-weight: 600;
  color: #313133;
}
.informartion {
  width: 75vw;
  word-wrap: break-word;
  border: 0px solid red;
  font-size: 3.5vw;
}
.checkbox {
  position: absolute;
  right: 4vw;
  width: 11vw;
  height: 6vw;
  border-radius: 5vw;
  appearance: none;
  border: 1px solid #eee;
  background: #fff url("/public/image/input/circle.png") no-repeat left;
  background-size: 56% 98%;
}
.checkbox:checked {
  width: 11vw;
  height: 6vw;
  border: 1px solid red;
  background: #fff url(/public/image/input/selectCircle.png) no-repeat right;
  background-size: 55% 100%;
}
.mask2 {
  z-index: 6;
  position: fixed;
  bottom: 0vw;
  left: 0vw;
  background: rgba(0, 0, 0, 0.5);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.remove {
  font-size: 3.2vw;
  font-weight: 700;
  color: red;
  padding: 2vw;
}
</style>



