<template>
  <div v-if='data.length != 0' class="image">
		<div>
			<swiper  :options="swiperOption" ref="mySwiper">
				<swiper-slide :key='index'  v-for='(item,index) in data.pic_urls'><!--轮播图-->
					<img class='image-img'  :src='item'>
				</swiper-slide>
				<div class="swiper-pagination"  slot="pagination"></div>
			</swiper>
		</div>
		<div class='goodsDetail-content'>
			<div class='goodsDetail-item'>
				<div class='goodsDetail-price'>
          <div class='goodsDetail-nowprice'>{{data.price}}</div>
          <div class='goodsDetail-lineprice'>{{data.line_price}}</div>
          <img 
            class='goodsDetail-img' 
            src='/public/image/goodsDetails/image/share.png'
            @click.stop='share'
          />
        </div>
				<div class='goodsDetail-name'>{{data.name}}</div>
			</div>
			<div class='goodsDetail-item-row'>
        <div class='goodsDetail-courier'>快递:{{data.kdf == 0 ? "包邮":data.kdf}}</div>
        <div class='goodsDetail-sales'>月销{{data.monthSale}}笔</div>
        <div class='goodsDetail-address'>{{data.address}}</div>
      </div>
		</div>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  name: 'goodsDetail-content',
  data () {
    return {
			swiperOption:{
				autoplay:{
					autoplay:true,
					delay: 3000,
					disableOnInteraction: false,
				},
        pagination:{
          el:'.swiper-pagination',
          clickable: true,
          bulletActiveClass:'active'
        },
				loop: {
          loop:true,
          loopedSlides:2
        },
        basic:{
          direction:'vertical',
        },
				speed: 1000,
				observer:true,//修改swiper自己或子元素时，自动初始化swiper
				observeParents:true,//修改swiper的父元素时，自动初始化swiper
			},
			data:{
				
			}
    }
  },
	components:{
		swiper,	
		swiperSlide
	},
	methods:{
		getGoodsDate:function(){  
			this.axios({
				method:'get',
				url:`shopGoods/${this.$route.query.id}`,
        key:sessionStorage.shopkey
			}).then((res)=>{
        if(res.data.status == 200){
          this.data = res.data.data;
          this.$emit('data',res.data.data);
        }else{
          alert(res.data.message)
        }
			})
		},
    share () {
      this.$emit('show','')
    },
	},
	created(){
		this.getGoodsDate();
    
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
*{
	margin:0;
	padding:0;
}
.image{
	border-bottom:1px solid #eee;
	margin-top:12vw;
}
.image-img{
	width:100vw;
  height:100vw;
}
html{
	background:red;
}
.goodsDetail-content{
	display:flex;
	flex-flow:column wrap;
	justify-content:space-between;
	align-items:center;
	background:#fff;
	padding:2vw 2vw;
}
.goodsDetail-item{
  width:100%;
  display:flex;
  justify-content:flex-start;
  align-items:flex-start;
  flex-flow:column wrap;
	font-size:6vw;
  flex:1;
}
.goodsDetail-name{
	flex:1;
	font-size:4.2vw;
}
.goodsDetail-price{
	color:red;
  position:relative;
  align-items:flex-end;
  display:flex;
  width:100%;
  .goodsDetail-nowprice{
    &:before{
      content:'￥';
      font-size:4.5vw;
    }
  }
  .goodsDetail-lineprice{
    font-size:3vw;
    margin-left:2vw;
    color:#888;
    text-decoration:line-through;
    &:before{
      content:'￥';
      text-decoration:line-through;
    }
  }
  .goodsDetail-img{
    position:absolute;
    right:2vw;
    top:0;
    width:6vw;
    height:6vw;
  }
}

.goodsDetail-item-row{
  display:flex;
  justify-content:space-between;
  align-items:center;
  flex:1;
  width:100%;
}
.goodsDetail-share{
	display:flex;
	justify-content:center;
	align-items:center;
	flex-flow:column wrap;
	font-size:3vw;
}
.goodsDetail-courier,.goodsDetail-sales,.goodsDetail-address{
	color:#999;
	font-weight:500;
	font-size:4vw;
	margin-top:1vw;
}
.share{
	width:6vw;
	margin-bottom:0.5vw;
}
>>>.active{ /* >>>为样式穿透*/
  opacity:1;
  background:red;
}
</style>



