/*商品分类*/
<template>
	<div class='classifyGoods'>
		<!-- 选项卡-start -->
		<div class='classifyGoods-scroll-hide'>
			<div class='classifyGoods-scroll'>
				<div :class='index == menuFlag ? "select":""' class='classifyGoods-meuns' v-for='(item,index) in menu' @click.stop='change(item.id,index)'>
					<div>{{item.text}}</div>
					<div v-if='index == menuFlag' class='classifyGoods-menu-bootom-line'></div>
				</div>
			</div>
		</div>
		<!-- 选项卡-end -->
		
		
		<!-- 内容页-start -->
		<div class='classifyGoods-item'>
			<div class='classifyGoods-content' v-for='(item,index) in menuGoods' :key='index'>
				<img :src='item.imgUrl' class='classifyGoods-img'>
				<div class='classifyGoods-text'>{{item.text}}</div>
				<div class='classifyGoods-price-sold'>
					<div class='classifyGoods-price'>￥{{item.price}}</div>
					<div class='classifyGoods-sold'>{{item.sold}}已售</div>
				</div>
			</div>
		</div>
		<!-- 内容页-end -->
		
		<!-- 加载动画-start -->
		<div class='loading'>
			<div>已经到最底部啦</div>
		</div>
			
		<!-- 加载动画-end -->
	</div>
</template>

<script>
export default {
  name: 'classifyGoods',
  data () {
    return {
			menu:[],
			menuGoods:[],
			menuFlag:0,
			menuGoodsIndex:1,
			pages:0,
    }
  },
	methods:{
		getMenus:function(){//获取菜单列表
			this.axios({
				url:'classifGoodsMenus',
				method:'get',
        params:{
          key:sessionStorage.shopkey
        }
			}).then((res)=>{
				if(res.data.status == 200){
					this.menu = res.data.data
				}else{
					
				}
			})
		},
		getMenusData:function(status){//获取菜单商品
			this.axios({
				url:'classifGoods',
				method:'get',
				params:{
					id:status,
          key:sessionStorage.shopkey
				}
			}).then((res)=>{
				if(res.data.status == 200){
					this.menuGoods = res.data.data;
					this.pages = res.data.pages;
				}else{
					
				}
			})
		},
		getMenusDataMore:function(data){//获取更多
			this.axios({
				url:'classifGoods',
				method:'get',
				params:{
					id:data,
          key:sessionStorage.shopkey
				}
			}).then((res)=>{
				if(res.data.status == 200){
					this.menuGoods.push(...res.data.data)
					this.pages = res.data.pages;
				}else{
					alert("数据获取失败")
				}
			})
		},
		go:function(data){
			// this.$router.push({name:data})
		},
		change:function(data,index){
			this.menuFlag = index;
			this.getMenusData(index)
		},
		listenscroll:function(){
			
		}
	},
	watch:{
		menuFlag:function(val,oldVal){
			this.menuGoodsIndex = 1;
		}
	},
	created:function(){
		this.getMenus();
		this.change(0,0)
		let that = this;
		window.addEventListener('scroll',function(){
			let scrollY = this.scrollY;
			let i = 1;
			if(document.body.scrollHeight <= scrollY+window.screen.height){//如果(网页高度 小于或等于 滚动条高度 + 浏览器高度)
				if(that.pages >= that.menuGoodsIndex){
					that.getMenusDataMore(++that.menuGoodsIndex);
				}else{
					console.log('我不输出了')
				}
			}
		},false);
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.classifyGoods{
	position:relative;
	overflow:hidden;
}
.classifyGoods-scroll-hide{
	width:100%;
	overflow:hidden;
	height:10vw;
	border-top:1px solid #eee;
	border-bottom:1px solid #eee;
}
.classifyGoods-scroll{
	width:100%;
	white-space: nowrap;
	overflow-x: scroll;
	height:20vw;
}
.classifyGoods-meuns{
	display:inline-block;
	width:20vw;
	text-align:center;
	height:10vw;
	line-height:10vw;
	position:relative;
}
.classifyGoods-menu-bootom-line{
	width:20vw;
	position:absolute;
	bottom:0vw;
	border:1px solid red;
}
.select{
	color:red;
}
.classifyGoods-item{
	width:100%;
	display:flex;
	justify-content:flex-start;
	align-items:center;
	flex-flow: row wrap;
	overflow:hidden;
	margin-top:2vw;
}
.classifyGoods-content{
	width:48vw;
	margin:1vw;
	overflow:hidden;
}
.classifyGoods-img{
	width:48vw;
	height:50vw;
	border-radius: 1vw 1vw;
}
.classifyGoods-text{
	font-size:3vw;
}
.classifyGoods-price-sold{
	display:flex;
	justify-content:space-between;
	align-items:center;
	font-size:2vw;
}
.classifyGoods-price{
	color:red;
}
.classifyGoods-sold{
	color:#999;
}
.loading{
	display:flex;
	justify-content:center;
	align-items:center;
	color:#eee;
	font-size:2vw;
}
</style>



