<template>
  <div class="chooseGoods">
		<div class='store-item'>
      <div class='store-content' @click.stop='show'>
        <div class='store-name'>选择</div>
        <div class='store-right'>
          <img src='/public/image/input/go.png' class='store-go'>
        </div>
      </div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'goodsDetail-chooseGoods',
  data () {
    return {
			data:[]
    }
  },
	methods:{
    /**
     * 显示layer
    */
		show:function(){
			this.$emit('show','')
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.chooseGoods{
	background:#fff;
	margin-top:1vw;
	margin-bottom:1vw;
	display:flex;
	flex-flow:row wrap;
	justify-content:flex-start;
	align-items:center;
	padding:2vw;
  padding-left:4vw;
}
.store-item{
	display:flex;
	width:96vw;
	justify-content:flex-start;
	align-items:center;
  padding-bottom:1vw;
  .store-content{
    flex:1;
    display:flex;
    justify-content:space-between;
    align-items:center;
    .store-name{
      font-size:3.5vw;
      &:after{
        content:'规格';
        font-size:3vw;
        color:#888;
        margin-left:1vw;
      }
    }
    .store-right{
      display: flex;
      justify-content:center;
      align-items:center;
      .store-right-text{
        font-size:3vw;
      }
      .store-go{
        width:4vw;
        height:4vw;
        margin: 0vw 1vw;
      }
    }
  }
}

</style>



