<template>
  <div class="ItemEvaluation">
    <div v-for='(item,index) in data'>
      <div class='ItemEvaluation-head'>
        <img class='ItemEvaluation-head-img' :src='item.pic_url'>
        <div class='ItemEvaluation-head-test'>描述相符</div>
        <star class='ItemEvaluation-head-star' :index='index' @nums='describe_score'></star>
      </div>
      <div class='ItemEvaluation-content'>
        <textarea v-model='item.content' placeholder='宝贝满足你的期待吗?说说你的使用心得,分享给想买的他/她们吧'></textarea>
        <div>
          <div class='ItemEvaluation-content-choose' @click.stop='chooseImg(index)' v-if='item.pics_url.length != 5' >
            <img src='/public/image/comments/picture.png'>
          </div>
          <div class='ItemEvaluation-content-img' v-for='(item,idx) in item.pics_url'>
            <img :src='item'>
            <!-- <img class='del' src='/public/image/comments/del.png' @click.stop='previewImage(index,idx)'> -->
          </div>
        </div>
      </div>
      <div class='ItemEvaluation-head'>
        <div class='ItemEvaluation-head-test'>物流服务</div>
        <star class='ItemEvaluation-head-star' :index='index' @nums='express_score'></star>
      </div>
      <div class='ItemEvaluation-head'>
        <div class='ItemEvaluation-head-test'>服务态度</div>
        <star class='ItemEvaluation-head-star' :index='index' @nums='service_score'></star>
      </div>
    </div>
  </div>
</template>

<script>
import star from './star' 
export default {
  name: 'comments',
  props:['data'],
  data () {
    return {
      commentData:[],
    }
  },
	components:{
		star,
	},
	methods:{
    /**
     * 描述相符评分
    */
    describe_score(data){
      let index = data.index;
      let source = data.source;
      this.data[index].describe_score = source;
    },
    /**
     * 物流服务评分
    */
    express_score(data){
      let index = data.index;
      let source = data.source;
      this.data[index].express_score = source;
    },
    /**
     * 服务态度评分
    */
    service_score(data){
      let index = data.index;
      let source = data.source;
      this.data[index].service_score = source;
    },
    /**
     * 选择图片接口
    */
    chooseImg(index){
      console.log(index)
      wx.chooseImage({
        count:5,
        sizeType:['original', 'compressed'],
        sourceType:['album', 'camera'],
        success:res => {
          this.data[index].pics_url.push(...res.localIds);
          this.data[index].serverId = [];
          this.poo(index);
        }
      })
    },
    async poo(index){
      for(let i of this.data[index].pics_url){
        this.data[index].serverId.push(await this.uploadImg(i))
      }
      
    },
    /**
     * 预览图片接口
    */
    previewImage(index,idx){
      wx.previewImage({
        current: this.data[index].pics_url[idx], // 当前显示图片的http链接
        urls: this.data[index].pics_url// 需要预览的图片http链接列表
      });
    },
    /**
     * 上传图片
    */
    async uploadImg(images){
      return await new Promise((resolve,reject) =>{
				wx.uploadImage({
					localId:images,
					isShowProgressTips: 1,
					success:function(res){
						resolve(res.serverId);
					},
					fail:function(res){
            reject(res)
					}
				})
			});
    }
	},
	mounted(){
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.ItemEvaluation{
  padding:0vw 2vw;
  background:#fff;
  overflow:hidden;
}
.ItemEvaluation-head{//标题
  display:flex;
  justify-content:flex-start;
  flex-flow:row nowrap;
  align-items:center;
  padding:2vw 0vw;
  background:#fff;
  border-bottom:1px solid #eee;
  .ItemEvaluation-head-img{//标题图片
    width:12vw;
    height:12vw;
    border-radius:3vw;
  }
  .ItemEvaluation-head-test{//标题文字
    font-size:4vw;
    font-weight:600;
    padding:0 2vw;
  }
  .ItemEvaluation-head-star{//星星宽度
    width:60vw;
  }
}
.ItemEvaluation-content{
  margin:1vw;
  border-bottom:1px solid #eee;
  textarea{
    width:96vw;
    border:0px solid red;
    height:20vw;
    font-weight:600;
    font-size:3.5vw;
    letter-spacing:0.1px;
  }
  div{
    display:flex;
    flex-flow:row wrap;
    justify-content:flex-start;
    align-items:center;
    .ItemEvaluation-content-choose{
      width:28vw;
      height:28vw;
      margin-right:2.65vw;
      margin-left:0.65vw;
      margin-bottom:2vw;
      display:flex;
      justify-content:center;
      align-items:center;
      flex-flex-flow:column wrap;
      border:1px dotted #eee;
      box-sizing: border-box;
      img{
        width:10vw;
        height:10vw;
      }
    }
    .ItemEvaluation-content-img{
      width:30vw;
      height:30vw;
      position:relative;
      display:flex;
      justify-content:center;
      align-items:center;
      margin-bottom:2vw;
      margin-right:0.65vw;
      margin-left:0.65vw;
      img{
        width:28vw;
        height:28vw;
      }
      .del{
        border-radius:50%;
        position:absolute;
        width:5vw;
        height:5vw;
        top:0vw;
        right:0vw;
      }
    }
  }
}
</style>



