<template>
  <div class="shopping-cart-dele">
		<div class='footer-left'>
			<input type='checkbox' v-model='chooseAllFlag' @change='change()'> 
			<div>全选</div>
		</div>
		<div>
		</div>
		<div class='footer-right'>
			<div class='footer-right-btn' @click.stop='delChecked'>删除所选</div>
		</div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: 'shoppingCartFooter',
  data () {
    return {
			chooseAllFlag:false
    }
  },
	props:['operation','cartdata'],
	methods:{
		change(){ //显示
			for(let i of this.cartdata){
					i.flag = this.chooseAllFlag ? true:false;
			}
		},
		delChecked(){ //删除数据
			let data = [];
			for(let i of this.cartdata){
				if(i.flag){
					data.push(i.id);
				}
			}
      data = data.toString();
      let a = {
        ids:data
      }
      this.axios({
        url:'shopCart',
        method:'delete',
        data:this.qs.stringify(a)
      }).then(res =>{
        if(res.data.status == 200){
          Toast({
            message:'删除成功',
            duration:1000
          });
          this.$emit('reload',res.data.data)
        }
        
      })
		}
	},
	created(){
		
	},
	watch:{
		cartdata:{//深度挖掘监听cartdata的值的改动
			handler(val,oldVal){
				this.chooseAllFlag = true;
				for(let i of this.cartdata){
					if(i.flag == false){
						this.chooseAllFlag = false;
					}
				}
				
			},
			deep:true
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.shopping-cart-dele{
	width:100vw;
	display:flex;
	justify-content:space-between;
	align-items:center;
	height:15vw;
	border-top:1px solid #eee;
	background:#fff;
  position:fixed;
  left:0;
  top:15vw;
}
.footer-left,.footer-right{
	display:flex;
	align-items:center;
	font-size:4vw;
}
.footer-right-btn{
	padding:2vw 5vw;
	background:#d20e10;
	font-size:3vw;
	margin: 0vw 2vw;
	border-radius:1vw;
	color:#fff;
}
input{
	margin:0vw 2vw;
	margin-left:4vw;
}
</style>



