<template>
  <div class="address">
		<div class='flex-row-wrap address-container' v-for='(item,index) in address' :key='item.id'>
			<div class='address-surnames'>{{item.name[0]}}</div>
			<div class='address-item'>
				<div class='flex-row-wrap'>
					<div class='address-name'>{{item.name}}</div>
					<div class='address-phone'>{{item.phone}}</div>
				</div>
					
				<div class='address-address'>
					<span class='address-default' v-if='item.is_default == 1'>默认</span>
					{{item | address}}
				</div>
			</div>
			<div class='address-edit' @click.stop='go(item.id)'>
				编辑
			</div>
		</div>
		<div class='footer' @click='go("add")'>添加新地址</div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      address:[],
    }
  },
  filters:{
    address(val){
      return val.province + val.city + val.area + val.street + val.address
    }
  },
	methods:{
		getaddress(){
			this.axios({
				url:'shopContact',
				method:'get'
			}).then(res=>{
        if(res.data.status == 204 || res.data.status == 200){
          this.address = res.data.data;
        }else if (res.data.status == 1001){
          this.$emit('login','');
        }
			})
		},
		go(id){
      if(sessionStorage.jwt == undefined || sessionStorage.jwt == 'undefined' || sessionStorage.jwt.length == 0){
        this.$emit('login','');
        return false
      }
			this.$emit('edit',id)
		}
	},
	created(){
		this.getaddress();
	},
	watch:{
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.address{
	min-height:94vh;
	background:#eee;
	margin-top:12vw;
  padding-bottom: 12vw;
}
.address-container{
	background:#fff;
	padding:3vw 2vw;
}
.address-surnames{
	width:10vw;
	height:10vw;
	background:#bec0c2;
	color:#fff;
	display:flex;
	justify-content:center;
	align-items:center;
	border-radius:50%;
	margin:0vw 2vw;
	font-size:4vw;
}
.address-item{
	flex:1;
	margin-right:2vw;
}
.address-name{
	font-size:4vw;
	color:#3e3e3e;
}
.address-phone{
	color:#aaabab;
	font-size:3.2vw;
	margin-left:2vw;
}
.address-address{
	font-size:3.2vw;
}
.address-default{
	padding:0vw 2vw;
	background:#fff1eb;
	color:#fe7135;
}
.address-edit{
	width:10vw;
	height:10vw;
	display:flex;
	justify-content:center;
	align-items:center;
	border-left:1px solid #eee;
	padding:0vw 2vw;
	font-size:3.2vw;
	color:#bbbbbb;
}
.footer{
  font-size:4vw;
}
</style>



