<template>
  <div class="shopping-cart-tabbar">
		<div class='shopping-cart-item'>
			<div>
				购物车
			</div>
			<div class='shopping-cart-operation' @click.stop='change'>
				{{status}}
			</div>
		</div>
		
  </div>
</template>

<script>
export default {
  name: 'shoppingCartFooter',
  data () {
    return {
			status:'编辑',
			flag:true
    }
  },
	props:['cartdata'],
	methods:{
		change(){
			this.flag = !this.flag;
			this.$emit('showtab',this.flag)
		}
	},
	created(){
		
	},
	watch:{
		flag(val,oldVal){
			this.status = this.flag ? '编辑':'完成';
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.shopping-cart-tabbar{
	position:fixed;
	height:15vw;
	width:100vw;
	background:white;
	top:0vw;
	border-bottom:1px solid #eee;
}
.shopping-cart-item{
	position:relative;
	display:flex;
	justify-content:center;
	align-items:center;
	height:100%;
}
.shopping-cart-operation{
	position:absolute;
	right:4vw;
}
</style>



