<template>
	<div class='like'>
		<img src='/public/image/index/like/cai.png' class='like-img'>
		<img :src='data[0].imgUrl' v-if='data.length != 0' @click='go(data[0].url)' class='like-goods-img'>
		<div class='like-goods-grid'>
			<img class='like-goods-grid-img' v-for='(item,index) in data' v-if='index != 0' :src='item.imgUrl' @click='go(item.url)'>
		</div>
	</div>
</template>

<script>
export default {
  name: 'like',
  data () {
    return {
			data:[]
    }
  },
	methods:{
		getList:function(){
			this.axios({
				url:'like',
				method:'get',
        params:{
          key:sessionStorage.shopkey
        }
			}).then((res)=>{
				if(res.data.status == 200){
					this.data = res.data.data
				}else{
					console.log('猜你喜欢获取失败',res.data);
				}
			})
		},
		go:function(data){
			// this.$router.push({name:data})
		}
	},
	created:function(){
		this.getList();
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.like{
	position: relative;
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	align-items: center;
	margin-top:4vw;
}
.like-img{
	position: absolute;
	top: -6vw;
	left: 1vw;
	width:20vw;
}
.like-goods-img{
	width:48vw;
	height:48vw;
}
.like-goods-grid{
	width:48vw;
	height:48vw;
	display:flex;
	flex-flow: row wrap;
	justify-content: space-between;
	align-items: center;
	margin-left:1vw;
}
.like-goods-grid-img{
	width:23vw;
	flex:1;
	margin-left:1vw;
}
</style>



