<!--订单详情的商品-->
<template>
  <div v-if='goods.data' class="orderItemGoods">
  
    <!--商品列表-->
    <div v-for='(item,index) in goods.data' @click='go(item.goods_id)' class='orderItemGoods-item'>
      <img :src='item.pic_url' class='orderItemGoods-item-img'>
      <div class='orderItemGoods-item-content'>
        <div class='orderItemGoods-item-left'>
          <div class='orderItemGoods-item-text'>{{item.name}}</div>
          <div class='orderItemGoods-item-type'>{{item.property1_name}} {{item.property2_name}}</div>
        </div>
        <div class='orderItemGoods-item-right'>
          <div class='orderItemGoods-item-price'>￥{{item.price}}</div>
          <div class='orderItemGoods-item-nums'>x{{item.number}}</div>
        </div>
      </div>
    </div>
    
    <!--商品信息-->
    <div class='goodsOrder'>
      <div class='goodsOrder-goods'>
        <div class='goodsOrder-item'>
          <div class='goodsOrder-item-left'>商品总价</div>
          <div class='goodsOrder-item-right'>￥{{goods.orderAllPrice}}</div>
        </div>
        <div class='goodsOrder-item'>
          <div class='goodsOrder-item-left'>运费(快递)</div>
          <div class='goodsOrder-item-right'>￥{{goods.express_price}}</div>
        </div>
        <div class='goodsOrder-item'>
          <div class='goodsOrder-item-left'>店铺优惠(红包)</div>
          <div class='goodsOrder-item-right'>-￥{{goods.voucher}}</div>
        </div>
      </div>
      
      <div class='goodsOrder-allPrice'>
        <h5 class='goodsOrder-item-left'>订单总价</h5>
        <h5 class='goodsOrder-item-right'>￥{{goods.total_price}}</h5>
      </div>
      <div class='goodsOrder-shifukuan'>
        <h4 class='goodsOrder-item-left'>实付款</h4>
        <h4 class='goodsOrder-item-right'>￥{{goods.payment_money}}</h4>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'orderItemGoods',
  props:['goods'],
  data () {
    return {
			
    }
  },
  created(){
		
	},
	components:{
		
	},
	methods:{
		go(goodsId){
      this.$router.push({name:'goodsDetails',query:{id:goodsId}})
    }
	},
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.orderItemGoods{
  margin-top:2vw;
  background:#fff;
  overflow:hidden;
  padding:2vw 2vw;
  
  .orderItemGoods-item{ //商品详情
    display:flex;
    justify-content:flex-start;
    align-items:flex-start;
    flex-flow:row wrap;
    margin-bottom:2vw;
    
    
    .orderItemGoods-item-img{ //商品图片
      width:20vw;
      height:20vw;
      margin-right:2vw;
    }
    
    .orderItemGoods-item-content{ //商品信息
      display:flex;
      justify-content:space-between;
      align-items:flex-start;
      flex:1;
      
      .orderItemGoods-item-left{ //商品信息文字描述
        display:flex;
        justify-content:center;
        flex-flow:column wrap;
        align-items:flex-start;
        flex:1;
        margin-right:2vw;
        
        .orderItemGoods-item-text{ //商品名称
          font-size:3.5vw;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          display: -webkit-box;
          text-overflow: ellipsis;
          overflow:hidden;
        }
        
        .orderItemGoods-item-type{ //商品分类
          font-size:3vw;
          margin-top:0.5vw;
          color:#999;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          display: -webkit-box;
          text-overflow: ellipsis;
          overflow:hidden;
        }
        
      }
      
      .orderItemGoods-item-right{ //商品价格描述
        width:15vw;
        text-align:right;
        .orderItemGoods-item-price{ //商品价格
          font-size:3.5vw;
        }
        
        .orderItemGoods-item-nums{ //商品数量
          font-size:3vw;
          margin-top:0.5vw;
          color:#999;
        }
      }
      
    }
    
  }
  
  .goodsOrder{ //商品价格之类的东西
    width:96vw;
    padding-top:2vw;
    margin-top:3vw;
    border-top:1px solid #eee;
    
    .goodsOrder-allPrice{ //订单总价
      color:#000;
      font-size:4vw;
      display:flex;
      justify-content:space-between;
      align-items:center;
      margin-bottom:2vw;
    }
    
    .goodsOrder-shifukuan{ //实付款
      display:flex;
      justify-content:space-between;
      align-items:center;
      padding-top:2vw;
      padding-bottom:1vw;
      border-top:1px solid #eee;
      
      .goodsOrder-item-right{
        color:#f36f40;
      }
      
    }
    
    .goodsOrder-goods{
      
      .goodsOrder-item{ //商品价格之类的详情
        display:flex;
        justify-content:space-between;
        align-items:center;
        color:#555;
        
        .goodsOrder-item-left{
          font-size:3.5vw;
          font-weight:600;
          margin-bottom:1.5vw;
        }
        
        .goodsOrder-item-right{
          font-size:3.5vw;
        }
        
      }
      
    }
    
    
  }
  
}

</style>



