<template>
  <div class="address">
		<headhistory :data='title' v-if='id == null'></headhistory>
		<container @login='login' v-if='id == null' @edit='edit'></container>
		<editAddress v-if='id != null' :id='id' @hide='hide'></editAddress>
    <login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import headhistory from'../history/headhistory'
import container from'./content'
import editAddress from'./addaddress'
import login from'../login/login'
export default {
  name: 'HelloWorld',
  data () {
    return {
      sheng:[],
			id:null,
			title:'我的地址',
      showFlag:false
    }
  },
	components:{
		container,
		editAddress,
		headhistory,
    login
	},
	methods:{
		edit(data){
			this.id = data;
		},
		hide(){
			this.id = null;
		},
    login (data) {
      this.showFlag = !this.showFlag;
    }
	},
	created(){
		
	},
	watch:{
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



