<template>
  <div class='list'>
		<div class='meuns'>
			<div class='menu-item' @click.stop='changeMenus(index,item.id)' v-for='(item,index) in menus' :class='index == menuFlag ? "select":""'>{{item.name}}</div>
		</div>
		<div class='list-secondary-classification'>
			<div class='list-secondary-classification-item' v-for='(item,index) in listItem' @click.stop='go(index)'>
				<img class='list-secondary-classification-img' :src='item.pic_url'>
				<div class='list-secondary-classification-text'>{{item.name}}</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'list',
  data () {
    return {
			menus:[],
			menuFlag:0,
			listItem:[]
    }
  },
	methods:{
		go:function(index){
			this.$router.push({
        name: 'classificationDetails',
        query:{
          name:this.listItem[index].name,//标题名
          m_id:this.listItem[index].id//二级分类id
        }
      });
		},
		getMenus:function(){//获取一级分类
			this.axios({
				method:'get',
				url:'shopCategory'
			}).then((res)=>{
				console.log(res);
				this.menus = res.data.data[0];
        let id = res.data.data[0][0].id;
        this.changeMenus(0)
			})
		},
		changeMenus:function(index){//获取二级分类
			console.log(index)
			this.menuFlag = index;
			this.axios({
				method:'get',
				url:`shopCategory/${this.menus[index].id}`
			}).then((res)=>{
				console.log(res);
				if(res.data.status == 200){
					this.listItem = res.data.data;
				}else{
          this.listItem = true;
        }
			})
		}
	},
	created:function(){
		this.getMenus();//获取一级分类
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.list{
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	flex-flow: row wrap;
	position: relative;
}
.meuns{
	width:20vw;
}
.menu-item{
	height:10vw;
	border:1px solid #eee;
	font-size:3vw;
	display:flex;
	justify-content:center;
	align-items:center;
}
.select{
	color:#ec6d69;
	background:#f5f5f5;
}

.list-secondary-classification{
	width:80vw;
	overflow:hidden;
	display:flex;
	justify-content:space-between;
	align-items:center;
	flex-flow:row wrap;
	box-sizing:border-box;
	border-left:1px solid #eee;
}
.list-secondary-classification:after{
	width:25vw;
  margin:0.5vw;
  content:'';
}
.list-secondary-classification-item{
	width:25vw;
	overflow:hidden;
	margin:0.5vw;
	display:flex;
	justify-content:flex-start;
	align-items:center;
	flex-flow:column wrap;
}

.list-secondary-classification-img{
	width:25vw;
}
.list-secondary-classification-text{
	font-size:3vw;
	margin-top:2vw;
	margin-bottom:3vw;
}
</style>



