<template>
  <div id="app">
    <router-view class='content'/>
		<div v-if='show' class='footer'>
			<div @click.stop='change(index)' class='footer-item' v-for='(item,index) in menus' :class='index == menuFlag ? "selectMenu":""'>
				<img :src='index == menuFlag ? item.selectImgUrl : item.imgUrl' class='footer-img'>
				<div>{{item.text}}</div>
			</div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'App',
	created () {
    console.log(this.qs.stringify({name:'咸鱼kang',old:'38'}))
		let flag = false;  //设置底部菜单栏是否显示
		for(let i in this.menus){
			if(this.$route.name == this.menus[i].url){
				this.menuFlag = i;
				flag = true;
			}
		}
		this.show = flag; //设置底部菜单栏是否显示
    this.getjwt(); //获取jwt
	},
	data(){
		return{
			menus:[
				{text:'首页',imgUrl:'/public/image/menu/shouye1.png',selectImgUrl:'/public/image/menu/shouye.png',url:'index'},
				{text:'购物车',imgUrl:'/public/image/menu/car1.png',selectImgUrl:'/public/image/menu/car.png',url:'shoppingCart'},
				{text:'我的',imgUrl:'/public/image/menu/wode1.png',selectImgUrl:'/public/image/menu/wode.png',url:'my'},
			],
			menuFlag:0,
			show:true
		}
	},
	methods:{
    /**
     * 重定向后的跳转
     * url 路由地址
     * data 路由后的数据
    */
    redirectBack () {
      try{
        let url = sessionStorage.url.split('?')[0];
        let data = {};
        for(let i of sessionStorage.url.split('?')[1].split('&')){
          data[i.split('=')[0]] = i.split('=')[1]
        }
        this.$router.push({path:url,query:data})
      }catch (err) {
        
      }
    },
    /**
     * 菜单栏切换
    */
		change(data){ //
			this.menuFlag = data;
			this.$router.push({name:this.menus[data].url,query:{key:sessionStorage.shopkey}});
		},
    /**
     * 获取jwt,用于鉴别用户信息,登录有效期
    */
    getjwt:function(){ 
      if(sessionStorage.jwt == undefined || sessionStorage.jwt.length == 0 || sessionStorage.jwt == 'undefined'){
        try{
          sessionStorage.setItem('jwt',this.$route.query.token);
        }catch(err){
          alert('token获取失败')
        }
      }else{
        
      }
      if(sessionStorage.shopkey == undefined || sessionStorage.shopkey == 'undefined'){
        try{
          sessionStorage.setItem('shopkey',this.$route.query.key);
        }catch(err){
          alert('token获取失败')
        }
      }else{
        
      }
      this.axios.defaults.headers.common['Access-Token'] = sessionStorage.jwt == 'undefined' ? '' : sessionStorage.jwt;  //设置axios请求头,
      this.axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';  //设置axios请求传值方式,
      this.getWeixinConfig();  //获取微信jssdk配置信息
      this.getShopInfo();
      if(sessionStorage.url != undefined || sessionStorage.url != 'undefined' || sessionStorage.url.length != 0){
        //this.redirectBack();
      }
		},
    /**
     * 获取微信jssdk配置信息
    */
    getWeixinConfig(){  
      this.axios({
        url:'shopConfig'
      }).then(res => {
        if(res.data.status == 200){
          wx.config({
            debug: true,
            appId: res.data.data.appId,
            timestamp: res.data.data.timestamp,
            nonceStr: res.data.data.nonceStr,
            signature: res.data.data.signature,
            jsApiList: [
              'updateAppMessageShareData',
              'updateTimeLineShareData',
              'chooseWXPay',
              'chooseImage',
              'uploadImage',
              'previewImage',
              'getLocalImgData'
            ]
          });
        }
        console.log(res.data)
      })
    },
    /**
     * 获取商城信息
    */
    getShopInfo () {
      this.axios({
        url:'ShopAppInfo',
        params:{
          key:sessionStorage.shopkey
        }
      }).then(res => {
        if(res.data.status == 200){
          document.title = res.data.data.name;
          sessionStorage.setItem('shopkey',res.data.data.key);
        }else{
          console.log(res.data.message)
        }
      })
    }
	},
	watch:{
		$route(to,from){ //监听路由切换
			let flag = false;
			for(let i in this.menus){//设置路由选中
				if(to.name == this.menus[i].url){
					this.menuFlag = i;
					flag = true;
				}
			}
			this.show = flag;
		}
	}
}

</script>

<style>
*{ 
	margin:0;
	padding:0;
  font-family:'myFirstFont'
}
input:focus{
	outline:none;
}
textarea:focus{
	outline:none;
}
input[type='checkbox']{
	width:5vw;
	height:5vw;
	border-radius:50%;
	appearance:none;
	border:1px solid #999;
}
input[type='checkbox']:checked{
	width:5vw;
	height:5vw;
	background:url('/public/image/input/selected.png') no-repeat center;
	background-size:80% 80%;
}

.footer{
	position:fixed;
	bottom:0vw;
	left:0vw;
	height:12vw;
	display:flex;
	align-items:center;
	justify-content:center;
	width: 100%;
	background:#fff;
	border-top:1px solid #eee;
	font-size:2vw;
  z-index:99;
}
.footer-item{
	flex:1;
	display:flex;
	flex-flow: column wrap;
	align-items:center;
	justify-content:center;
}
.footer-img{
	width:6vw;
}
.selectMenu{
	color:#69c2aa;
}
.flex-row{
	display:flex;
	align-items:center;
	justify-content:flex-start;
}
.flex-row-wrap{
	display:flex;
	align-items:center;
	justify-content:flex-start;
	flex-flow:row wrap;
}
.flex-row-between{
	display:flex;
	align-items:center;
	justify-content:space-between;
	flex-flow:row wrap;
}
.flex-column-between{
	display:flex;
	align-items:flex-start;
	justify-content:space-between;
	flex-flow:column wrap;
}
.align-top{
	align-items:flex-start;
}
.mask{
	z-index:6;
	position:fixed;
	bottom:0vw;
	left:0vw;
	background:rgba(0,0,0,0.5);
	width:100vw;
	height:100vh;
}
.layer{
	width:96vw;
	height:80vh;
	padding:0vw 2vw;
	padding-top:2vw;
	background:#fff;
	position:fixed;
	bottom:0vw;
	border-top-left-radius:2vw 2vw;
	border-top-right-radius:2vw 2vw; 
}
.layer-buttom-btn{
	border-radius:5vw;
	background:linear-gradient(to right,#ff7301,#ff4b03);
	display:flex;
	justify-content:center;
	align-items:center;
	color:#fff;
	width:96vw;
	font-size:4vw;
	height:40px;
}
</style>



