<template>
  <div class="recommended">
    <div class='recommended-title'>
      <img src='/public/image/payOk/tuijian.png'>
      <div>推荐商品</div>
    </div>
    <div class='goods'>
      <div class='recommended-item' v-for='(item,index) in data' @click='go(item.id)'>
        <img class='recommended-item-img' :src='item.imgUrl'>
        <div>{{item.text}}</div>
      </div>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'recommended',
  data () {
    return {
      title:'支付结果',
			data:'1029.00'
    }
  },
  created(){
		this.getGoods();
	},
	components:{
	},
	methods:{
    getGoods(){
      this.axios({
        url:'http://192.168.188.61:3000/spellGroup'
      }).then(res=>{
        console.log(res);
        this.data = res.data.data;
      })
    },
    go(data){
      this.$router.push({name: 'goodsDetails',query:{id:data}});
    }
	}
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.recommended{
  display:flex;
  flex-flow:row wrap;
  align-items:center;
  justify-content:center;
  
  .recommended-title{
    display:flex;
    flex-flow:row wrap;
    align-items:center;
    width:96vw;
    justify-content:flex-start;
    
    img{
      width:6vw;
      height:6vw;
      margin:2vw;
    }
    
    div{
      font-size:4vw;
      font-weight:600;
    }
  }
  
  .goods{
    display:flex;
    flex-flow:row nowrap;
    align-items:center;
    justify-content:space-between;
    width:92vw;
    
    .recommended-item{
      display:flex;
      flex-flow:row wrap;
      align-items:center;
      justify-content:center;
      width:29vw;
      
      .recommended-item-img{
        width:20vw;
        height:20vw;
        border-radius:50%;
      }
      
      div{
        font-size:3vw;
        margin-top:2vw;
        overflow: hidden;
        text-overflow:clip;
        white-space: nowrap;
        font-weight:600;
      }
    }
  }
  
}
</style>



