<template>
  <div class="goodsDetails">
		<headhistory :data='title' v-if='id != null'></headhistory>
		<!-- 图片 -->
			<imgs @data='getData' @show='showShare' ></imgs>
    
    <!--购物车图标-->
    <img  src='/public/image/goodsDetails/header/gouwuche.png' @click.stop='go' class='fixed-img'/>
    
		<!-- 优惠券 -->
			<!-- <coupons></coupons> -->
		
    <!-- 分类 -->
			<chooseGoods @show='showChooseGoods'></chooseGoods>
      
		<!-- 评论 -->
			<evaluation :goodsid='id'></evaluation>
      
		<!-- 商家信息 -->
			<store></store>
    
		<!-- 商品描述 -->
      <detail></detail>
      
		<!-- 底部 -->
			<detailsFooter @login='login' ref='footer'></detailsFooter>
      
    <!-- 分享 -->
			<share :data='data' ref='share'></share>
      
      <login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
import imgs from './image' 
import evaluation from './evaluation' 
import store from './store' 
import chooseGoods from './chooseGoods' 
import coupons from './coupons' 
import detail from './details' 
import detailsFooter from './detailsFooter' 
import share from './share' 
import login from'../login/login'
export default {
  name: 'goodsDetails',
  data () {
    return {
			title:'商品详情',
      id:this.$route.query.id,
      data:{},
      showFlag:false
    }
  },
	components:{
		headhistory,
		imgs,
		evaluation,
		store,
		chooseGoods,
		coupons,
		detail,
		detailsFooter,
		share,
    login
	},
	methods:{
    go () {
      this.$router.push({name:'shoppingCart'})
    },
    showChooseGoods () {
      this.$refs.footer.show()
    },
    showShare () {
      this.$refs.share.show()
    },
    getData (data) {
      console.log('商品数据',data)
      this.data = data;
    },
    login (data) {
      this.showFlag = !this.showFlag;
    }
	},
	created () {
		if(this.$route.query.id == undefined){
			this.$router.push({name:'index'})
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.goodsDetails{
	margin-bottom:12vw;
	background:#f5f5f5;
}
.goodsInfo{
  background:#fff;
}
.fixed-img{
  width:9vw;
  height:9vw;
  position:fixed;
  right:3vw;
  top:15vw;
  border:0px solid red;
  z-index:4;
  border-radius:50%;
  opacity:.5;
}
</style>



