<template>
	<div class="head-history">
		<div class="address-title">
			<img src='/public/image/input/back.png' class='back' @click.stop='back'>
			<div>{{data}}</div>
		</div>
	</div>
  
</template>

<script>
export default {
  name: 'orderHeader',
	props:['data'],
  data () {
    return {
      
    }
  },
	components:{
		
	},
	methods:{
		back(){
			window.history.go(-1);
		}
	},
	created(){
		
	},
	watch:{
		
	}
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.head-history{
	position:fixed;
	top:0vw;
	left:0vw;
	width: 100vw;
	height:12vw;
	z-index: 999;
	background: #fff;
	border-bottom:1px solid #eee;
}
.back{
	width:5vw;
	height:5vw;
	position:absolute;
	left:2vw;
}
.address-title{
	display:flex;
	justify-content:center;
	align-items:center;
	position:relative;
	height:12vw;
}
</style>



