<template>
  <div class='header'>
		<div class='bar'>
			<input type='text' @click='go("search")' class='search'>
		</div>
		<div v-if='swiperData.length != 0'>
			<swiper  :options="swiperOption" ref="mySwiper">
				<swiper-slide  :key='index'  v-for='(item,index) in swiperData'>
          <img :src='item.pic_url'>
					
				</swiper-slide>
        <div class="swiper-pagination"  slot="pagination"></div>
			</swiper>
		</div>
	</div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import { Toast } from 'mint-ui';
var vm = null;
export default {
  name: 'banner',
	components:{
		swiper,
		swiperSlide
	},
  data () {
    return {
			swiperOption:{
				autoplay:{
					autoplay:true,
					delay: 3000,
					disableOnInteraction: false,
				},
        pagination:{
          el:'.swiper-pagination',
        },
				loop: true,
				speed: 1000,
				on:{
					tap:function(){
						vm.goGoodsDeatils(vm.swiperData[this.realIndex].jump_url)
					}
				},
         preventLinksPropagation: false
			},
			swiperData:[]
    }
  },
	methods:{
		go:function(data){
			this.$router.push({name: data});
		},
    goGoodsDeatils(data){
      location = data;
      return false;
      this.$router.push({name: 'goodsDetails',query:{id:data}});
    },
		getbanner:function(){
			this.axios({
				url:'ShopBanner',
				method:'get',
        params:{
          key:sessionStorage.shopkey,
          type: 1
        }
			}).then((res)=>{
				if(res.data.status == 200){
					this.swiperData = res.data.data;
				}else{
          console.log('轮播图获取失败',res.data)
				}
			})
		}
	},
	computed:{
		swiper(){
			return this.$refs.mySwiper.swiper;
		}
	},
	created:function(){
		this.getbanner();
		vm = this;
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header{
	background:#f4f5f9;
}
img{
	width:100%;
	height:50vw;
}
.bar{
	position:absolute;
	top:0vw;
	z-index:2;
	display:flex;
	justify-content:center;
	align-items:center;
	width:100%;
  background:rgba(0,0,0,0.1);
  height:10vw;
}
.list{
	width:8vw;
	height:6vw;
	margin-right:3vw;
	margin-left:3vw;
}
.search{
	width:70vw;
	height:6vw;
	border-radius:5vw 5vw;
	border:1px solid #fff;
	background:#fff5;
	padding-left:5vw;
}
.search:focus{
	outline: none;
}
</style>



