<template>
  <div v-if='true' @click='couponsShow("show")' class="coupons">
		<div class='coupons-text'>优惠</div>
		<div>
			<div v-for='(item,index) in data' class='flex-row-wrap coupons-item'>
				<img :src='item.imgUrl' class='coupons-item-img'>
				<div class='coupons-item-text'>{{item.text}}</div>
			</div>
		</div>
		<div class='coupons-go'>
			<div>领券</div>
			<img class='coupons-img' src='/public/image/my/menus/前进.png'>
		</div>
		
		<div v-if='layerFlag'  class='mask'>
			<div class='layer'>
				<div class='coupons-layer-title'>优惠</div>
				<div class='coupons-layer-header'>领券</div>
				<div class='coupons-layer-informaotion'>
					<div>
						<div class='coupons-layer-item' v-for='(item,index) in couponsData'>
							<div class='coupons-layer-content'>
								<div>￥<span class='coupons-layer-price'>{{item.price}}</span> {{item.type}}</div>
								<div>{{item.conditions}}</div>
								<div>{{item.validity}}</div>
							</div>
							<div class='coupons-layer-get' @click.stop='get(item.id)'>立即领取</div>
						</div>
					</div>
				</div>
				<div @click.stop='couponsShow("close")' class='layer-buttom-btn'>关闭</div>
			</div>
		</div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'goodsDetail-store',
  data () {
    return {
			data:[],
			layerFlag:false,
			couponsData:[]
    }
  },
	methods:{
		getData(){
			this.axios({
				url:'getcoupons',
				method:'post',
				data:{
					id:1
				}
			}).then((res)=>{
				this.data = res.data.data;
			})
		},
		getCouponsData(){
			this.axios({
				url:'getcouponslayer',
				method:'post',
				data:{
					id:1
				}
			}).then((res)=>{
				this.couponsData = res.data.data;
			})
		},
		couponsShow(data){
			if(data == 'show'){
				this.layerFlag = true;
			}else if(data == 'close'){
				this.layerFlag = false;
			}
		},
		get(data){
			
		}
	},
	created(){
		this.getData();
		this.getCouponsData();
	},
	watch:{
		layerFlag(val,oldVal){
			if(val){
				this.$nextTick(()=>{
					let wrapper = document.querySelector('.coupons-layer-informaotion');
					let scroll = new BScroll(wrapper,{
						click:true
					})
				})
			}
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.coupons{
	background:#fff;
	margin-top:1vw;
	display:flex;
	flex-flow:row wrap;
	justify-content:space-between;
	align-items:flex-start;
	padding:2vw;
}
.coupons-text{
	color:red;
	font-size:3.6vw;
	font-weight:600;
}
.coupons-item{
	margin-bottom:2vw;
}
.coupons-item-img{
	width:5vw;
}
.coupons-item-text{
	font-size:3.5vw;
	overflow:hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	width:60vw;
	margin-left:0.5vw;
}
.coupons-go{
	display:flex;
	align-items:center;
	font-size:3.2vw;
	color:#707070;
}
.coupons-img{
	width:5vw;
	height:5vw;
}
.coupons-layer-title{
	display:flex;
	flex-flow:row wrap;
	justify-content:center;
	align-items:center;
	font-size:4.5vw;
	padding:1vw;
}
.coupons-layer-header{
	padding-bottom:1vw;
}
.coupons-layer-informaotion{
	width:96vw;
	overflow:hidden;
	height:65vh;
	margin-bottom:2vw;
}
.coupons-layer-item{
	margin-top:3vw;
	background:#fff2e6;
	position:relative;
	padding: 2vw 2vw;
	color:#ff952e;
	display:flex;
	justify-content:start;
	align-items:center;
	flex-flow:row wrap;
}
.coupons-layer-content{
	width:61.8vw;
	border-right:1px dashed #ffc791;
	font-size:3.2vw;
}
.coupons-layer-price{
	font-size:6vw;
}
.coupons-layer-get{
	width:27vw;
	margin-left:2vw;
	height:16vw;
	display:flex;
	justify-content:center;
	align-items:center;
	font-size:4vw;
}
.coupons-layer-item::after{
	width:4vw;
	height:4vw;
	background:#fff;
	position:absolute;
	top:-2vw;
	content:"";
	border-radius:50%;
	right:30vw;
}
.coupons-layer-item::before{
	width:4vw;
	height:4vw;
	background:#fff;
	position:absolute;
	bottom:-2vw;
	content:"";
	border-radius:50%;
	right:30vw;
}
.layer-buttom-btn{
	background:#ff9901;
}
</style>



