<template>
  <div class="search">
    <headhistory :data='title'></headhistory>
    <div class='search-input-item'>
      <input class='search-input' v-model='conditions' placeholder='请输入查询的商品名称,譬如:短裤'>
      <div class='btn' @click.stop='searchdata'>搜索</div>
    </div>
    <searchHistory ref='searchHistory' :showFlag='historyshow' @data='getdata'></searchHistory>
    <!-- <searchList ref='searchList' :keywords='conditions'  @data='get'></searchList> -->
    <searchGoods :id='data'></searchGoods>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
import searchHistory from './searchHistory' 
import searchList from './searchList' 
import searchGoods from './searchGoods' 
export default {
  name: 'search',
  data () {
    return {
      title:'搜索',
			conditions:'',
      data:'',
      historyshow:true
    }
  },
	components:{
		headhistory,
    searchHistory,
    searchList,
    searchGoods
	},
	methods:{
		get(id){
      this.data = id;
      this.historyshow = false;
    },
    searchdata(){
      this.data = this.conditions;
      let localdata = '';
      try{
        localdata = localStorage.searchList.split(',');
      }catch(err){
        localdata = [];
      }
      
      localdata.push(this.conditions);
      localStorage.searchList = localdata;
      //this.$refs.searchList.showFlag = false;
      this.historyshow = false;
    },
    getdata(data){
      this.conditions = data;
    }
	},
	created(){
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.search{
  padding:2vw 2vw;
  margin-top:24vw;
  display:flex;
  align-items:center;
  justify-content:center;
  background:#f2f2f2;
  overflow:hidden;
  flex-flow:row wrap;
  .search-input-item{
    background:#eee;
    position:fixed;
    top:12vw;
    left:0vw;
    display:flex;
    width:100vw;
    padding-top:2vw;
    z-index:9;
    background:#f2f2f2;
    justify-content:flex-end;
    align-items:center;
    .search-input{
      height:10vw;
      flex:1;
      background:#fff;
      border:none;
      font-size:4vw;
      border-radius:5vw;
      border:1px solid #eee;
      padding:0 5vw;
      margin-left:2vw;
    }
    .btn{
      width:15vw;
      background:#fff;
      height:10vw;
      border-radius:5vw;
      font-size:4vw;
      display:flex;
      justify-content:center;
      align-items:center;
      margin:0vw 2vw;
    }
  }
  
}
</style>


