<template>
  <div class="comments">
		<headhistory :data='title' v-if='true'></headhistory>
    <div class='sub' @click.stop='sub'>发布</div>
    <ItemEvaluation :data='data' @stars='describe'></ItemEvaluation>
    <login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
import ItemEvaluation from './ItemEvaluation' 
import login from'../login/login'
import score from './score' 
import { Indicator } from 'mint-ui';
export default {
  name: 'comments',
  data () {
    return {
			title:'发表评价',
      data:[
      ],
      describe_score:0,
      showFlag: false
    }
  },
	components:{
		headhistory,
		ItemEvaluation,
		score,
	},
	methods:{
    getData(){
      this.axios({
        url:`shopOrder/${this.$route.query.order_sn}`
      }).then(res => {
        let arr=[];
        for(let i of res.data.data.order){
          let data = {
            order_id:i.id,
            pic_url:i.pic_url,
            content:'',
            pics_url:[],
            serverId:[],
            describe_score:'',
            express_score:'',
            service_score:'',
          }
          arr.push(data);
        }
        console.log()
        this.data = arr;
      })
    },
		sub(){
      console.log(this.data);
      let params = {
        data:JSON.stringify(this.data),
        order_sn:this.$route.query.order_sn,
        class:1
      }
      Indicator.open();
      this.axios({
        url:'shopGoodsComment',
        method:'post',
        data:this.qs.stringify(params)
      }).then(res => {
        Indicator.close();
        if(res.data.status == 200) {
          this.$router.replace({name:'order'})
        }else{
          Toast(res.data.message);
        }
      })
    },
    describe(data){ //设置描述评分
      this.describe_score = data;
    },
    login (data) {
      this.showFlag = !this.showFlag;
    }
	},
	created(){
		this.getData();
    if(sessionStorage.jwt == undefined || sessionStorage.jwt == 'undefined' || sessionStorage.jwt.length == 0){
      this.login();
      return false
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.comments{
  margin-top:13vw;
}
.sub{
  position:fixed;
  top:0vw;
  right:0vw;
  padding:0 2vw;
  z-index:999;
  height:12vw;
  display:flex;
  justify-content:center;
  align-items:center;
  color:#ff4f00;
  font-weight:700;
  font-size:3.6vw;
}
</style>



