<template>
  <div class="classificationDetails">
		<headhistory :data='title'></headhistory>
		<classificationDetailsItem></classificationDetailsItem>
  </div>
</template>

<script>
import headhistory from'../history/headhistory'
import classificationDetailsItem from'./classificationDetailsItem'
export default {
  name: 'classificationDetails',
	components:{
		headhistory,
    classificationDetailsItem
	},
  data () {
    return {
      title:'分类详情'
    }
  },
	methods:{
		
	},
	created(){
    this.title = this.$route.query.name;
    console.log(this.title)
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.classificationDetails{
	margin-bottom:1vw;
}
</style>



