<template>
  <div class="evaluation">
		<div class='evaluation-nums'>商品评价({{data ? data.length:0}})</div>
		<div class='evaluation-more' @click='go()'>
			<div>查看全部</div>
			<img class='evaluation-more-img' src='/public/image/input/go.png'>
		</div>
		<div v-if='data && data.length != 0' class='evaluationDate'>
			<div class='evaluationDate-people'>
				<img class='evaluationDate-headerimg' :src='data[0].avatar'>
				<div class='evaluationDate-name'>{{data[0].nickname}}</div>
			</div>
			<div class='evaluationDate-text'>{{data[0].content}}</div>
      <div class='evaluationDate-scroll' v-if='data[0].pics_url != ""'>
        <img class='evaluationDate-img'  v-for='(item,index) in data[0].pics_url' :src='item'>
      </div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'evaluation',
  props:{
    goodsid:String
  },
  data () {
    return {
			data:[],
			flag:1
    }
  },
	methods:{
		getEvaluationDate:function(){  //获取商品评价
			this.axios({
				method:'get',
				url:`shopGoodsComments/${this.goodsid}`,
        params:{
          key:sessionStorage.shopkey
        }
			}).then((res)=>{
				this.data = res.data.data;
        console.log(this.data)
			})
		},
		go:function(data){
      this.$router.push({name:'commentsDetail',query:{id:this.goodsid,key:this.$route.query.key}});
		}
	},
	created(){
		this.getEvaluationDate(); //获取商品评价
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.evaluation{
	margin-top:1vw;
	margin-bottom:1vw;
	background:#fff;
	padding-left:4vw;
	padding-right:2vw;
	position:relative;
	padding-bottom:2vw;
  overflow:hidden;
}
.evaluation-nums{
	font-size:4vw;
	padding-top:2vw;
}
.evaluation-more{
	position:absolute;
	top:2vw;
	right:2vw;
	display:flex;
	align-items:center;
	font-size:3.2vw;
}
.evaluation-more-img{
	width:4vw;
	height:4vw;
	margin: 0vw 1vw;
}
.evaluation-menu{
	display:flex;
	margin:2vw 0vw;
}
.evaluation-item{
	font-size:3vw;
	margin-right:2vw;
	background:#ffebea;
	border:1px solid #ffebea;
	color:#645656;
	display:flex;
	justify-content:center;
	align-items:center;
	padding:0.5vw 2vw;
	border-radius:5vw 5vw;
}
.evaluationDate{
	display:flex;
	position:relative;
	flex-flow:column wrap;
  margin-top:2vw;
}
.evaluationDate-people{
	display:flex;
	align-items:center;
}
.evaluationDate-headerimg{
	width:6vw;
	height:6vw;
	border-radius:50%;
}
.evaluationDate-name{
	margin-left:2vw;
	font-size:3.5vw;
	color:#999;
}
.evaluationDate-text{
	color:#464646;
	font-size:4vw;
	margin-top:1.5vw;
}
.evaluationDate-scroll{
  width:100vw;
  height:30vw;
  overflow:hidden;
  white-space: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
}
.evaluationDate-img{
  height:30vw;
  margin-right:2vw;
}
</style>



