<template>
  <div class="createorder">
		<headhistory :data='title'></headhistory>
		<chooseAddress @changeaddress='getaddress'></chooseAddress>
		<goodsData :cartdata='goodData'></goodsData>
    <other ref='hongbao' :price='price' :yunfeiid='chooseaddress' @counpons='counpons' @postage='postage'></other>
    <div class='footer' @click.stop='heiheihei'>
      <div class='left'>
        合计:<span>￥{{price}}</span>
      </div>
      <div class='right'>提交订单</div>
    </div>
    
    <login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import headhistory from'../history/headhistory'
import chooseAddress from'./chooseAddress'
import goodsData from'./goodsData'
import other from'./other'
import login from'../login/login'
import { Toast } from 'mint-ui';
export default {
  name: 'createorder',
  data () {
    return {
      title:'创建订单',
			hongbao:'',
      goodData:'',
      price:'',
      youfei:0,
      chooseaddress:'选中的地址数据',
      counponsprice:0,
      showFlag:false
    }
  },
	components:{
    chooseAddress,
    headhistory,
    goodsData,
    other,
    login
	},
  created(){
    if(sessionStorage.jwt == undefined || sessionStorage.jwt == 'undefined' || sessionStorage.jwt.length == 0){
      this.login();
      return false
    }
    this.goodData = JSON.parse(sessionStorage.data);
    this.price = this.$route.query.price;
	},
	methods:{
    heiheihei(){
      let data = {
        user_contact_id:this.chooseaddress.id, //收货地址id
        goods:JSON.stringify(this.goodData), //商品信息
        voucher_id:this.$refs.hongbao.counponsdata.id, //红包id
        remark:this.$refs.hongbao.message, //备注,
      }
      this.axios({
        url:'shopOrderPay',
        data:this.qs.stringify(data),
        method:'post'
      }).then(res => {
        if(res.data.status == 200){
          this.$router.replace({name:'orderItem',query:{order_sn:res.data.data.order_sn}})
        }else{
          Toast(res.data.message);
        }
      })
    },
    counpons(data){
      this.price = this.$route.query.price*1 - data*1 + this.youfei;
      this.counponsprice = data*1;
    },
    postage(data){//计算邮费
      this.youfei = data*1;
      this.price = this.$route.query.price*1 + data*1 - this.counponsprice*1;
    },
    getaddress(data){//获取收货地址
      console.log(data);
      this.chooseaddress = data;
      this.yunfeiid
    },
    login (data) {
      this.showFlag = !this.showFlag;
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.createorder{
  padding-top:12vw;
  padding-bottom:12vw;
}
.left{
  width:68vw;
  font-size:3.5vw;
  text-align:right;
  padding-right:2vw;
}
.right{
  width:30vw;
  height:100%;
  color:#fff;
  font-size:3.6vw;
  display:flex;
  justify-content:center;
  align-items:center;
  background:linear-gradient(to right,#ff7301,#ff4b03);
}
</style>



