<template>
  <div v-if='showFlag' class="searchGoods">
    <div class='flex-row-wrap'>
      <div @click='go(item.id)' class='goods-item' v-for='(item,index) in data'>
        <div class='goods-item-flex'>
          <img :src='item.stock.pic_url' class='goods-img'>
          <div class='goods-text'>{{item.stock.name}}</div>
          <div class='goods-content'>
            <div class='goods-price'>￥<span>{{item.price}}</span></div>
            <div class='goods-sold'>{{item.sold}}人付款</div>
          </div>
        </div>
      </div>
    </div>
    <div class='noData' v-if='dataNone'>暂无数据</div>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
import BScroll from 'better-scroll'
export default {
  name: 'searchGoods',
  props:['id'],
  data () {
    return {
      data:'',
      showFlag:false,
      dataNone:false
    }
  },
	components:{
		headhistory
	},
	methods:{
		choose(data){
      console.log(data);
    },
    getdata(data){
      this.axios({
        url:'shopGoods',
        params:{
          searchName:this.id
        }
      }).then(res => {
        console.log(res.data.data);
        if(res.data.status == 200){
          this.data = res.data.data;
          this.dataNone = false;
        }else{
          this.dataNone = true;
        }
        this.showFlag = true;
      })
    },
    go(data){
      console.log(data);
      this.$router.push({name:'goodsDetails',query:{id:data}});
    }
	},
	created(){
    
	},
  watch:{
    id(val){
      this.getdata();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.searchGoods{
  width:96vw;
  overflow:hidden;
  .searchGoods-item{
    padding:4vw;
  }
}
.flex-row-wrap{
  align-items:flex-start;
}
.goods-item{
	display:inline-block;
	margin:0.5vw;
  width:46vw;
}
.goods-item-flex{
	display:flex;
	flex-flow: column wrap;
	align-items:flex-start;
  justify-content:flex-start;
  background:#fff;
  margin-bottom:2vw;
  box-shadow:0vw 1vw 1vw #ddd;
  padding-bottom:3vw;
}
.goods-img{
	width:46vw;
	height:46vw;
}
.goods-text{
	font-size:3.5vw;
  overflow:hidden; 
  text-overflow:clip;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2; 
  margin:2vw 2vw;
  
}
.goods-content{
  width:96%;
  display:flex;
  flex-flow:row wrap;
  align-items:center;
  justify-content:space-between;
  .goods-price{
    font-size:2vw;
    color:red;
    padding:0vw 1vw;
    span{
      font-size:4.5vw;
    }
  }
  .goods-sold{
    color:#aaa;
    font-size:3vw;
  }
}
.noData{
  position:fixed;
  left:0;
  top:0;
  width:100vw;
  height:100vh;
  display:flex;
  justify-content:center;
  align-items:center;
  z-index:-1;
}
</style>



