<template>
	<div v-if='data' class='coupons'>
		<div class='coupons-scroll'>
			<div class='coupons-item' v-for='(item,index) in data' :key='item.id' @click='getCoupons(item.id)'>
        <div>
          <div class='left'>
            <div class='price'>
              ￥
              <span>{{item.price | Price}}</span>
            </div>
            <div class='content'>
              <div>{{item.name}}</div>
              <div>满{{item.full_price}}元可用</div>
            </div>
          </div>
          <div class='right'>
            <div>立即领取</div>
          </div>
        </div>
      </div>
		</div>
	</div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: 'coupons',
  data () {
    return {
			data:[]
    }
  },
	methods:{
		getList:function(){
			this.axios({
				url:'ShopRedEnvelope',
				method:'get',
        params:{
          key:sessionStorage.shopkey
        }
			}).then((res)=>{
				if(res.data.status == 200){
					this.data = res.data.data
				}else{
          this.data = '';
				}
			})
		},
		getCoupons:function(data){
			// this.$router.push({name:data})
      let ajaxData = {
        type_id:data
      }
      this.axios({
        method:'post',
        url:'ShopRedEnvelope',
        data:this.qs.stringify(ajaxData)
      }).then( res => {
        if(res.data.status == 200){
          Toast({
            message:"优惠券领取成功",
            duration:1000
          })
        }else if(res.data.status == 500){
          Toast({
            message:res.data.message,
            duration:1000
          })
        }
      })
		}
    
	},
	created:function(){
		this.getList();
    
	},
  filters:{
    Price(val){
      return val.substring(0,val.length-1);
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.coupons{
	overflow:hidden;
  background:#f3efdd;
  padding:2vw;
}
$coupons-scroll-background:#f3efdd;
.coupons-scroll{
	width:96vw;
	white-space: nowrap;
	overflow: scroll;
  overflow-y:hidden;
  height:18vw;
  background:$coupons-scroll-background;
  
  
  .coupons-item{//红包详情
    display:inline-block;
    background:linear-gradient(to bottom,#ff63a2, #ff0166);
    font-size:3vw;
    color:#fff;
    margin-right:2vw;
    
    &:last-child{
      margin-right:0vw;
    }
    div{
      display:flex;
      flex-flow:row wrap;
      
      .left{
        padding:4vw 2vw;
        border-right:#fff 1px dotted;
        position:relative;
        display:flex;
        flex-flow:row wrap;
        justify-content:center;
        align-items:center;
        
        :before{
          content:'';
          width:2vw;
          height:2vw;
          background:$coupons-scroll-background;
          position:absolute;
          top:-1vw;
          right:-1vw;
          border-radius:50%;
        }
        
        :after{
          content:'';
          width:2vw;
          height:2vw;
          background:$coupons-scroll-background;
          position:absolute;
          bottom:-1vw;
          right:-1vw;
          border-radius:50%;
        }
        
        .price{//价格
          display:flex;
          justify-content:center;
          align-items:flex-end;
          
          span{
            font-size:7vw;
          }
        }
        
        .content{//内容
          display:flex;
          flex-flow:column wrap;
          justify-content:center;
          align-items:center;
          margin-left:1vw;
        }
        
      }
      
      .right{
        padding:2vw;
        display:flex;
        flex-flow:row wrap;
        justify-content:center;
        align-items:center;
        width:7vw;
        
        div{
          width:7vw;
          white-space:normal
        }
        
      }
    }
    
  }
}
</style>



