<template>
  <div class="comments">
		<headhistory :data='title' v-if='true'></headhistory>
    <div>
      <div v-for='(item,index) in data' :key='item.id' class='comments-item'>
        <div class='comment-user'>
          <img :src='item.avatar' class='comments-head'>
          <div>{{item.nickname}}</div>
        </div>
        <div class='comment-time'>
          <div class='comment-data'>{{item.create_time}}</div>
          <div class='comment-classification'>{{item.property1_name}}{{item.property2_name}}</div>
        </div>
        <div class='comment-text'>
          {{item.content}}
        </div>
        <div class='comment-imgs'>
          <img class='comment-img' v-for='(src,index) in item.pics_url' :src='src' @click='lookImg(index,item.pics_url)'>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 

export default {
  name: 'comments',
  data () {
    return {
			title:'评价',
      data:[
      ]
    }
  },
	components:{
		headhistory,
		
	},
	methods:{
		getData(id){
      this.axios({
				method:'get',
				url:`shopGoodsComments/${this.$route.query.id}`,
        params:{
          key:this.$route.query.key
        }
			}).then((res)=>{
				this.data = res.data.data;
        console.log(this.data)
			})
    },
    lookImg(index,data){
      console.log(index,data)
      wx.previewImage({
        current: data[index], // 当前显示图片的http链接
        urls: data // 需要预览的图片http链接列表
      });
    }
	},
	created(){
		this.getData(1);
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.comments{
  margin-top:13vw;
  background:#eee;
  overflow:hidden;
  .comments-item{
    background:#fff;
    padding:2vw;
    display:flex;
    margin-bottom:2vw;
    justify-content:flex-start;
    align-items:flex-start;
    flex-flow:column wrap;
    .comment-user{//用户
      display:flex;
      align-items:center;
      justify-content:flex-start;
      .comments-head{//头像
        width:10vw;
        border-radius:50%;
        margin-right:2vw;
      }
    }
    .comment-time{
      display:flex;
      align-items:center;
      justify-content:flex-start;
      color:#aaa;
      font-size:3.5vw;
      margin:2vw 0vw;
      .comment-data{
        
      }
      .comment-classification{
        margin-left: 2vw;
      }
    }
    .comment-text{//文字
      font-size:4vw;
      font-weight:500;
    }
    .comment-imgs{
      display:flex;
      flex-flow: row wrap;
      justify-content:flex-start;
      align-items:center;
      .comment-img{
        width:30vw;
        height:30vw;
        margin:1vw 2vw 1vw 0vw;
      }
    }
  }
}

</style>



