<template>
  <div class="star">
    <img class='star-img' :src='item ? "/public/image/comments/starselected.png":"/public/image/comments/star.png"' v-for='(item,index) in data' @click='choose(index)'>
    <div class='evaluation'>{{evaluation | Evaluation}}</div>
  </div>
</template>

<script>
export default {
  name: 'comments',
  props:{
    index: Number
  },
  data () {
    return {
			title:'发表评价',
      data:[
        false,
        false,
        false,
        false,
        false,
      ],
      evaluation:''
    }
  },
	methods:{
		choose(index){
      for(let i in this.data){
        this.data[i] = false;
      }
      for(let i in this.data){
        if(i > index){
          break;
        }
        this.$set(this.data,i,true);
      }
      this.evaluation = ++index;
      let data = {
        source:this.evaluation,
        index:this.index
      }
      this.$emit('nums',data);
    }
	},
	created(){
		
	},
  filters:{
    Evaluation(val){
      switch(val){
        case 1:
          return '非常差';
          break;
        case 2:
          return '差';
          break;
        case 3:
          return '中';
          break;
        case 4:
          return '好';
          break;
        case 5:
          return '非常好';
          break;
        case '':
          return ' '
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.star{
  display:flex;
  flex-flow: row nowrap;
  justify-content:space-between;
  align-items:center;
  
}
.star-img{
  width:6vw;
  height:6vw;
}
.evaluation{
  font-size:4vw;
  width:14vw;
  text-align:right;
  color:#ccc;
  font-weight:600;
}
</style>



