<template>
	<div class='list'>
		
			<swiper  :options="swiperOption" ref="mySwiper">
				<swiper-slide :key='index'  v-for='(item,index) in list'><!--轮播图-->
          <div class="head-item">
            <div v-for="(item,index) in item" class='flex' @click='go(item.id,item.name)'>
              <img :src='item.pic_url' class='list-img'>
              <div class='list-text'>{{item.name}}</div>
            </div>
          </div>
				</swiper-slide>
			</swiper>
			
		
	</div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import { Toast } from 'mint-ui';
export default {
  name: 'classify',
  data () {
    return {
      swiperOption:{
				autoplay:false,
        basic:{
          direction:'vertical',
        },
				speed: 1000,
				observer:true,//修改swiper自己或子元素时，自动初始化swiper
				observeParents:true,//修改swiper的父元素时，自动初始化swiper
			},
			list:[
				
			]
    }
  },
  components:{
    swiper,	
		swiperSlide
  },
	methods:{
		getList:function(){
			this.axios({
				url:'shopCategory',
				method:'get',
        params:{
          key:sessionStorage.shopkey
        }
			}).then((res)=>{
        console.log(res);
				if(res.data.status == 200){
					this.list = res.data.data
				}else{
					console.log('商品分类数据获取失败',res.data)
				}
			})
		},
		go:function(id,name){
			this.$router.push({name:'classificationDetails',query:{id:id,name:name,key:sessionStorage.shopkey}})
		}
	},
	created:function(){
		this.getList();
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.list{
	background:#f4f5f9;
	padding-bottom:2vw;
}
.head-item{
	display:flex;
	justify-content:flex-start;
	align-items:center;
	flex-flow:row wrap;
  margin:2vw 3vw;
}
.flex{
	display:flex;
	justify-content:center;
	align-items:center;
	flex-flow:column wrap;
	width:23vw;
	margin-top:2vw;
}
.list-img{
	width:13vw;
	height:13vw;
	border-radius:100%;
	background:linear-gradient(to bottom,#9fece4, #2fd1cc);
	display:flex;
	justify-content:center;
	align-items:center;
}


.list-text{
  width:100%;
  text-align:center;
	color:#555;
  font-size:3vw;
  margin-top:2vw;
  letter-spacing:1px;
  white-space: nowrap;
  overflow:hidden; 
}
</style>



