import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index/index'  //首页
import classification from '@/components/classification/classification'  //分类
import my from '@/components/my/my'   //我的
import goodsDetails from '@/components/goodsDetails/goodsDetails'   //商品详情
import shoppingCart from '@/components/shoppingCart/shoppingCart'   //购物车
import address from '@/components/address/index'    //地址
import order from '@/components/order/order'    //订单
import classificationDetails from '@/components/classificationDetails/classificationDetails'    //分类详情
import comments from '@/components/comments/comments'   //发表评价
import commentsDetail from '@/components/commentsDetail/commentsDetail'   //评价信息
import createOrder from '@/components/createOrder/createOrder'   //创建订单
import coupons from '@/components/coupons/coupons'   //红包
import search from '@/components/search/search'   //搜索
import payOk from '@/components/payOk/payOk'   //支付完毕
import orderItem from '@/components/orderItem/orderItem'   //订单详情
import logisticsInformation from '@/components/logisticsInformation/logisticsInformation'   //物流信息
import applicationForDrawback from '@/components/applicationForDrawback/applicationForDrawback'   //售后申请(也叫退款)
import returnOrder from '@/components/returnOrder/returnOrder'   //售后订单


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },{
      path: '/classification',
      name: 'classification',
      component: classification
    },{
      path: '/my',
      name: 'my',
      component: my
    },{
      path: '/goodsDetails',
      name: 'goodsDetails',
      component: goodsDetails
    },{
      path: '/shoppingCart',
      name: 'shoppingCart',
      component: shoppingCart
    },{
      path: '/address',
      name: 'address',
      component: address
    },{
      path: '/order',
      name: 'order',
      component: order
    },{
      path: '/classificationDetails',
      name: 'classificationDetails',
      component: classificationDetails
    },{
      path: '/comments',
      name: 'comments',
      component: comments
    },{
      path: '/commentsDetail',
      name: 'commentsDetail',
      component: commentsDetail
    },{
      path: '/createOrder',
      name: 'createOrder',
      component: createOrder
    },{
      path: '/coupons',
      name: 'coupons',
      component: coupons
    },{
      path: '/search',
      name: 'search',
      component: search
    },{
      path: '/payOk',
      name: 'payOk',
      component: payOk
    },{
      path: '/orderItem',
      name: 'orderItem',
      component: orderItem
    },{
      path: '/logisticsInformation',
      name: 'logisticsInformation',
      component: logisticsInformation
    },{
      path: '/applicationForDrawback',
      name: 'applicationForDrawback',
      component: applicationForDrawback
    },{
      path: '/returnOrder',
      name: 'returnOrder',
      component: returnOrder
    }
  ]
})



