<template>
  <div class="shoppingCart">
    <!--导航条-->
		<tabbar @showtab='showtab' :cartdata='cartdata' @change='change'></tabbar>
    
    <!--购物车数据-->
		<cartitem :cartdata='cartdata' :show='show' @change='change' @reload='getcartData'></cartitem>
    
    <!--操作条-->
		<settlement :show='show' :cartdata='cartdata' ></settlement>
    
    <login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import cartitem from './cartitem'
import settlement from './settlement'
import tabbar from './tabbar'
import login from'../login/login'
export default {
  name: 'shoppingCart',
  data () {
    return {
			cartdata:[],
			show:true,
      showFlag:false
    }
  },
	watch:{
		
	},
	components:{
		cartitem,
		settlement,
		tabbar,
    login
	},
	methods:{
		getcartData(){ //获取购物车数据
			this.axios({
				url:'shopCart',
				method:'get'
			}).then((res)=>{
				console.log(res.data)
        if(res.data.status == 200){
          this.cartdata = res.data.data
        }else if (res.data.status == 1001) {
          this.showFlag = true;
        }else if(res.data.status == 204){
          this.cartdata = []
        }
			})
		},
		change(data){ //切换(忘了想怎么做的了)
			console.log(this.cartdata[data].flag)
		},
		showtab(data){ // 显示条子
			this.show = data;
			for(let i of this.cartdata){
				i.flag = false;
			}
		},
    login (data) {
      this.showFlag = !this.showFlag;
    }
	},
	created(){
		this.getcartData();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.shoppingCart{
	
}
</style>



