<template>
  <div class="cart">
		<div class='row'>
      <div>店铺优惠</div>
      <div @click='showhngbao'>
        <div v-if='counponsdata.price != 0'>满{{counponsdata.full_price}}元减{{counponsdata.price}}</div>
        <div v-if='counponsdata.price == 0'>不使用优惠券</div>
      </div>
    </div>
    <div class='row'>
      <div>配送方式</div>
      <div>{{youfei | Youfei}}</div>
    </div>
    <div class='row'>
      <div>买家留言:</div>
      <input v-model='message' placeholder='选填,填写内容已和卖家协商确认'>
    </div>
    <div class='right'>
      小计:￥{{price}}元 
    </div>
    
    <!--选择红包页面-->
    <div v-show='layerFlag' class='mask'>
      <div class='layer'>
        <div id='scroll'>
          <div class='scroll-child'>
            <div class='nocoupons' @click='close'>有钱任性,不使用优惠券</div>
            <div class='coupons' v-if='item.status == 1 && item.full_price <= Voucher' v-for='(item,index) in hongbao' :key='item.id' @click='choose(item)'>
              <div class='left'>
                <div>￥<span>{{item.price | Price}}</span>{{item.type_name}}</div>
                <div>订单金额满{{item.full_price}}元可用</div>
                <div>{{item.start_time}}-{{item.end_time}}</div>
              </div>
              <div class='right'>立即使用</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'cart',
  data () {
    return {
			hongbao:'',
      message:'',
      layerFlag:true,
      counponsdata:'',
      youfei:'',
      Voucher:0
    }
  },
  props:['price','yunfeiid'],
	components:{
		
	},
  created(){
    this.gethongbao();
    this.counponsdata = {
      price:0
    };
    this.Voucher = this.price
	},
  mounted(){
    
  },
  filters:{
    Price(val){
      return val.substring(0,val.length-1);
    },
    Youfei(val){
      if(val == '0.00' || val == ''){
        val = '免快递费'
      }else{
        val = val += '元'
      }
      return val;
    }
  },
  watch:{
    hongbao(val){
      this.$nextTick(() => {
        this.layerFlag = false;
        let wrapper = document.querySelector('#scroll')
        let scroll = new BScroll(wrapper,{
          scrollY:true,
          click:true
        })
      })
    },
    counponsdata(val){
      console.log(val.price);
      this.$emit('counpons',val.price);
    },
    yunfeiid(val){
      this.shopKdf();
    }
  },
	methods:{
    gethongbao(id){
			this.id = id;
			this.axios({
				url:'ShopVoucher',
				method:'get'
			}).then(res => {
        if(res.data.status == 200){
          this.hongbao = res.data.data;
        }else if(res.data.status == 204){
          this.hongbao = [];
        }
      })
		},
    showhngbao(){
      if(this.layerFlag){
        this.layerFlag = false;
      }else if(!this.layerFlag){
        this.layerFlag = true;
      }
    },
    choose(data){
      this.counponsdata = data;
      this.layerFlag = false;
    },
    close(){
      this.layerFlag = false;
      this.counponsdata = {
        price:0
      };
    },
    shopKdf(){//获取邮费
      console.log(JSON.parse(sessionStorage.data));
      let data = [];
      for(let i of JSON.parse(sessionStorage.data)){
        let goodsData = {
          stock_id:i.stock_id,
          number:i.number
        }
      }
      this.axios({
        url:'shopKdf/'+this.yunfeiid.id,
        params:JSON.parse(sessionStorage.data)[0]
      }).then(res => {
        if(res.data.status == 200){
          this.youfei = res.data.data;
          this.$emit('postage',res.data.data)
        }
      })
    }
	},
	
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.cart{
  background:#fff;
}
.row{
  display:flex;
  flex-flow: row nowrap;
  justify-content:space-between;
  align-items:center;
  border-bottom:1px solid #eee;
  padding:2vw 2vw;
  font-size:4vw;
}
input{
  width:75vw;
  font-size:3.5vw;
  border:none;
}
.right{
  display:flex;
  justify-content:flex-end;
  padding:2vw;
  font-size:3.5vw;
}

.coupons{
  display:flex;
  flex-flow:row nowrap;
  justify-content:center;
  background:#fff2e6;
  color:#ff952e;
  border-radius:1vw;
  margin-bottom:2vw;
  .left{
    border-right:1px dashed #ffc791;
    position:relative;
    padding:2vw;
    width:60vw;
    :before{
      content:'';
      position:absolute;
      top:-2vw;
      right:-2vw;
      background:#fff;
      width:4vw;
      height:4vw;
      border-radius:50%;
    }
    :after{
      content:'';
      position:absolute;
      bottom:-2vw;
      right:-2vw;
      background:#fff;
      width:4vw;
      height:4vw;
      border-radius:50%;
    }
    div{
      font-size:3vw;
    }
    span{
      font-size:5vw;
      margin:0vw 1vw;
    }
  }
  .right{
    width:30vw;
    display:flex;
    align-items:center;
    justify-content:center;
  }
}
.layer{
  height:66vh;
}
#scroll{
  overflow:hidden;
  height:62vh;
}

.scroll-child{
  
}

.nocoupons{
  display:flex;
  flex-flow:row nowrap;
  justify-content:center;
  background:#fff2e6;
  color:#ff952e;
  border-radius:1vw;
  margin-bottom:2vw;
  padding:2vw;
  width:100%;
}
</style>



