<template>
	<div class='spellGroup'>
		<div class='spellGroup-label'>拼团</div>
		<div class='spellGroup-scroll'>
			<div class='spellGroup-content'>
				<div v-for='(item,index) in data' class='spellGroup-item' @click.stop='go(item.url)'>
					<div class='spellGroup-flex'>
						<img :src='item.imgUrl' class='spellGroup-img'>
						<div class='spellGroup-two'>
							<div class='spellGroup-status-nums'>
								<div class='spellGroup-status'>{{item.status == 0 ? '已团':'拼团'}}</div>
								<div class='spellGroup-nums'>{{item.nums}}件</div> 
							</div>
							<div>￥{{item.price}}</div>
						</div>
						<div class='spellGroup-text'>{{item.text}}</div>
					</div>
				</div>
			</div>
		</div>
		<div class='more'>更多>>></div>
	</div>
</template>

<script>
export default {
  name: 'spellGroup',
  data () {
    return {
			data:[]
    }
  },
	methods:{
		getList:function(){
			this.axios({
				url:'spellGroup',
				method:'get',
        params:{
          key:sessionStorage.shopkey
        }
			}).then((res)=>{
				if(res.data.status == 200){
					this.data = res.data.data;
          //this.go();
				}else{
					console.log('红包获取失败',res.data);
				}
			})
		},
		go:function(data){
			this.$router.push({name:'goodsDetails',query:{name:data}})
		}
	},
	created:function(){
		this.getList();
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.spellGroup{
	display: flex;
	flex-flow: column wrap;
	margin-top: 3vw;
	position:relative;
	overflow: hidden;
	margin-bottom:2vw;
}
.spellGroup-label{
	font-size: 4vw;
	color: #fff;
	background: red;
	width: 10vw;
	text-align: center;
	border-top-right-radius: 1vw 1vw;
	border-bottom-right-radius: 1vw 1vw;
}
.spellGroup-scroll{
	width:100%;
	height:50vw;
	overflow: hidden;
	padding-bottom:1.5vw;
}
.spellGroup-content{
	width:100%;
	white-space: nowrap;
	overflow-x: scroll;
	height:55vw;
}
.spellGroup-item{
	display:inline-block;
	margin:1vw 1vw;
	overflow:hidden;
}
.spellGroup-flex{
	display: flex;
	flex-flow: column wrap;
	justify-content:center;
	align-items:flex-start;
	width:30vw;
}
.spellGroup-img{
	width:30vw;
	height:35vw;
}
.spellGroup-two{
	width:30vw;
	display:flex;
	align-items:center;
	justify-content:space-between;
	font-size:2vw;
	color:red;
	margin:1vw 0;
}
.spellGroup-status-nums{
	border:1px solid red;
	background:red;
	display:flex;
	justify-content:center;
	align-items:center;
	width:20vw;
}
.spellGroup-status{
	background:#fff;
	text-align:center;
	flex:1;
}
.spellGroup-nums{
	color:#fff;
	flex:1;
	text-align:center;
}
.spellGroup-text{
	width:30vw;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	font-size:2vw;
}
.more{
	position:absolute;
	right:2vw;
	bottom:0vw;
	font-size:2vw;
	color:#555;
}
</style>



