<template>
  <div v-if='show' class="shopping-cart-footer">
		<div class='footer-left'>
			<input type='checkbox' v-model='flag' @change='change(flag)'> 
			<div>全选</div>
		</div>
		<div class='footer-right'>
			<div>合计:￥{{price}}</div>
			<div class='footer-right-btn' @click='jiesuan'>结算</div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'shoppingCartFooter',
  data () {
    return {
			flag:false,
			price:0,
      data:[]
    }
  },
	props:['cartdata','show'],
	methods:{
		change(data){ //改变状态
			for(let i of this.cartdata){
				i.flag = data;
			}
		},
		jiesuan(){ //结算商品
      if(this.data.length == 0) return false;
      sessionStorage.data = JSON.stringify(this.data);//将即商品参数存放到session里,跳转页面后自动读取
			this.$router.replace({name:'createOrder',query:{price:this.price}})//将总价传到订单中
		}
	},
	created(){
		
	},
	watch:{
		cartdata:{//深度挖掘监听cartdata的值的改动
			handler(val,oldVal){
				this.flag = true;
				this.price = 0;
        this.data = [];
				for(let i of this.cartdata){
					if(i.flag == false){
						this.flag = false;
					}else{
						this.price += i.price*i.number;//得出选中的总价格,我有预感,以后得改
            console.log(i.id)
            this.data.push(i);
					}
				}
			},
			deep:true
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.shopping-cart-footer{
	position:fixed;
	bottom:12vw;
	width:100vw;
	display:flex;
	justify-content:space-between;
	align-items:center;
	height:15vw;
	border-top:1px solid #eee;
	background:#fff;
}
.footer-left,.footer-right{
	display:flex;
	align-items:center;
	font-size:4vw;
}
.footer-right-btn{
	padding:2vw 5vw;
  background:linear-gradient(to right, #ff7301, #ff4b03);
  color:#ffffff;
	font-size:3vw;
	margin: 0vw 2vw;
	border-radius:1vw;
}
input{
	margin:0vw 2vw;
	margin-left:4vw;
}

</style>



