<template>
  <div class="orderItem">
		<div class='orderItem-item' @click.stop='goItem(item.order_sn)' v-for='(item,index) in data'  :key='item.id'>
			<!---->
			<div class='orderItem-content' v-for='(data,index) in item.order' :key='index'>
				<img class='orderItem-img' :src='data.pic_url'>
				<div class='orderItem-text'>
					<div class='orderItem-title'>{{data.name}}</div>
					<div class='orderItem-classification'>{{ data | one }}</div>
				</div>
				<div class='orderItem-unit'>
					<div class='orderItem-unit-price'>￥{{data.price}}</div>
					<div class='orderItem-nums'>x{{data.number}}</div>
				</div>
			</div>
      
			<div class='orderItem-total-price'>共{{item.numbers}}件商品 合计:￥{{(item.total_price * 1 - item.express_price * 1).toFixed(2)}}</div>
			<div class='orderItem-bar'>
        <div class='orderItem-status'>{{item.status | Title}}</div>
        <div class='flex-row-wrap'>
          <div v-if='item.status == 0' class='orderItem-bar-item' @click.stop='unpay(item.order_sn)'>取消订单</div>
          <div v-if='item.status == 0' class='orderItem-bar-item' @click.stop='pay(item.order_sn)'>付款</div>
          <div v-if='item.status != 0 && item.status !=7 && item.status != 2 && item.status != 6' class='orderItem-bar-item' @click.stop='returnGoods(item.order_sn)'>退货</div>
          <div v-if='item.status == 3 || item.status == 6' class='orderItem-bar-item' @click.stop='checkLogistics(item.order_sn)'>查看物流</div>
          <div v-if='item.status == 3' class='orderItem-bar-item' @click.stop='getGoods(item.order_sn)'>确认收货</div>
          <div v-if='item.status == 6' class='orderItem-bar-item' @click.stop='evaluation(item.order_sn)'>评价</div>
          <div v-if='item.status == 6 || item.status == 7 || item.status == 2' class='orderItem-bar-item' @click.stop='delOrder(item.order_sn)'>删除订单</div>
        </div>
			</div>
		</div>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui';
import { Toast } from 'mint-ui';
import { Indicator } from 'mint-ui';
export default {
  name: 'orderItem',
	props:['id'],
  data () {
    return {
			data:[]
    }
  },
	components:{
		
	},
	created(){
    console.log(this.$route.query.id)
	},
	watch:{
		id(val,oldVal){
      this.data = '';
      this.getList(val);
		}
	},
  filters:{
    Title(val){
      if(val == 0) val = '未付款';
      else if(val == 1) val = '待发货';
      else if(val == 3) val = '待收货';
      else if(val == 6) val = '已收货';
      else if(val == 7) val = '已完成';
      else if(val == 2) val = '已关闭';
      return val;
    },
    one(val){
      return val.property1_name + ',' + val.property2_name
    }
  },
  methods:{
    /**
     * 获取订单列表
    */
		getList(id){
      Indicator.open('Loading...');
			this.axios({
				url:'shopOrder',
				method:'get',
				params:{
					status:id
				}
			}).then(res=>{
				if(res.data.status == 200){
					this.data = res.data.data;
          Indicator.close();
				}else if(res.data.status == 204){
          this.data = [];
          Indicator.close();
        }else if(res.data.status == 1001){
          Indicator.close();
        }else{
          Indicator.close();
          Toast(res.data.message);
        }
        
			})
		},
    /**
     * 付款
    */
    pay(data){
      this.$router.push({name:'orderItem',query:{order_sn:data}})
    },
    /**
     * 取消订单
    */
    unpay(order_sn){
      MessageBox.confirm('是否取消订单?').then(action => {
        this.axios({
          url:`shopUnOrder/${order_sn}`,
          method:'put'
        }).then(res => {
          if(res.data.status == 200){
            this.getList(this.id)
          }else{
            Toast(res.data.message);
          }
        })
      });
    },
    /**
     * 退货跳转
    */
    returnGoods(order_sn){
      MessageBox.confirm('是否想要退货?').then(action => {
        this.$router.replace({name:'applicationForDrawback',query:{order_sn:order_sn}})
      });
    },
    /**
     * 查询物流
    */
    checkLogistics(order_sn){
      this.$router.push({name:"logisticsInformation",query:{order_sn:order_sn}})
    },
    /**
     * 确认收货
    */
    getGoods(id){
      MessageBox.confirm('确定已收货?').then(action => {
        console.log('执行确认收货操作?订单编号为:',id)
        let data = {
          order_sn: id
        }
        this.axios({
          url:'shopOrderGoods',
          method:'put',
          data:this.qs.stringify(data)
        }).then(res => {
          Toast('收货完毕');
          this.getList(this.id) //刷新当前页
        })
      });
    },
    /**
     * 评价
    */
    evaluation(order_sn){//评价
      this.$router.push({name:"comments",query:{order_sn:order_sn}})
    },
    /**
     * 查看详情
    */
    goItem(order_sn){
      this.$router.push({name:'orderItem',query:{order_sn:order_sn}})
    },
    /**
     * 删除订单
    */
    delOrder(order_sn){
      MessageBox.confirm('是否删除订单?').then(action => {
        Indicator.open('Loading...');
        this.axios({
          url:`shopOrder/${order_sn}`,
          method:'delete'
        }).then(res => {
          console.log(res);
          Indicator.close();
          if(res.data.status == 200){
            Toast('删除订单成功');
            this.getList(this.id)
          }else{
            Toast(res.data.message);
          }
        })
      });
    }
	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.orderItem{
	display:flex;
	flex-flow:column wrap;
	justify-content:flex-start;
	align-items:center;
	padding:2vw;
	background:#eee;
	font-size:3.2vw;
  margin-top:22vw;
  min-height:85vh;
}
.orderItem-item{
	background:#fff;
	width:90vw;
	margin:1.5vw;
	border-radius:2vw;
	padding:2vw 2vw;
  box-shadow:0px 3px 5px #ccc;
}
.orderItem-status{
	text-align:right;
  color:#aaa;
}
.orderItem-content{
	display:flex;
	justify-content:flex-start;
	align-items:flex-start;
  flex:1;
  margin-bottom:2vw;
  
}
.orderItem-img{
	width:20vw;
	height:20vw;
}
.orderItem-text{
  flex:1;
  margin:0vw 2vw;
}
.orderItem-title{
  font-size:3.5vw;
}
.orderItem-classification{
  color:#aaa;
  font-size:3.2vw;
}
.orderItem-unit{
  text-align:right;
}
.orderItem-unit-price{
  
}
.orderItem-nums{
  color:#aaa;
}
.orderItem-total-price{
	text-align:right;
  border-top:1px solid #eee;
  padding-top:2vw;
}
.orderItem-bar{
	display:flex;
	justify-content:space-between;
  align-items:center;
  margin-top:3vw;
}
.orderItem-bar-item{
  font-size:3.2vw;
  color:#aaa;
  border:1px solid #aaa;
  border-radius:3vw;
  margin-left:2vw;
  padding:1vw 3vw;
}
</style>



