<template>
  <div class="address">
		<div class='mask'>
			<div class='layer'>
				<div class='select-title'>
					<div>配送至</div>
					<img @click='hide' src='/public/image/input/close.png' class='close'>
				</div>
				<div class='select-header'>
					<div :class='showFlag == "province" ? "select":""' class='select-header-menu' @click='changeList("province")'>{{province.name}}</div>
					<div :class='showFlag == "city" ? "select":""' class='select-header-menu' @click='changeList("city")'>{{city.name}}</div>
					<div :class='showFlag == "area" ? "select":""' class='select-header-menu' @click='changeList("area")'>{{area.name}}</div>
					<div :class='showFlag == "street" ? "select":""' class='select-header-menu' @click='changeList("street")'>{{street.name}}</div>
				</div>
				<transition name="fade">
					<div v-if='showFlag=="province"' class='select-item' id='province'>
						<div>
							<div class='select-text' @click.stop='changeProvince(index,"province","sheng","city")'  v-for='(item,index) in sheng'>{{item.name}}</div>
						</div>
					</div>
				</transition>
				<transition name="fade">
				<div v-if='showFlag=="city"' class='select-item ' id='city'>
					<div>
						<div class='select-text' @click.stop='change(index,"city","shi","area")'  v-for='(item,index) in shi'>{{item.name}}</div>
					</div>
				</div>
				</transition>
				<transition name="fade">
				<div v-if='showFlag=="area"' class='select-item ' id='area'>
					<div>
						<div class='select-text' @click.stop='change(index,"area","qu","street")' v-for='(item,index) in qu'>{{item.name}}</div>
					</div>
				</div>
				</transition>
				<transition name="fade">
				<div v-if='showFlag=="street"' class='select-item ' id='street'>
					<div>
						<div class='select-text' @click.stop='getaddress(index)' v-for='(item,index) in jiedao'>{{item.name}}</div>
					</div>
				</div>
				</transition>
			</div>
		</div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'selectCity',
  data () {
    return {
      sheng:[],
			shi:[],
			qu:[],
			jiedao:[],
			province:'',
			city:'',
			area:'',
			street:'',
			showFlag:""
    }
  },
	methods:{
		getGoodsList:function(){
			this.axios({
				url:'goodAddress',
				method:'get',
			}).then((res)=>{
				console.log(res.data.data.districts[0].districts);
				this.sheng = res.data.data.districts[0].districts;
				let data = {
					name:res.data.data.districts[0].districts[0].name,
					id:res.data.data.districts[0].districts[0].adcode
				}
				this.province = data
			})
		},
		change(index,my,oneself,child){
			let data = {
				name:this[oneself][index].name,
				id:this[oneself][index].adcode
			}
			this[my] = data;
			this.showFlag = child;
		},
		changeProvince(index){
			let data = {
				name:this.sheng[index].name,
				id:this.sheng[index].adcode
			}
			this.province = data;
			this.showFlag = "city"
		},
		getlist(area,id,shi){
			this.axios({
				url:'goodAddress',
				method:'get',
				params:{
					keywords:id
				}
			}).then((res)=>{
				console.log(res);
				this[shi] = res.data.data.districts[0].districts;
				let data = {
					name:res.data.data.districts[0].districts[0].name,
					id:res.data.data.districts[0].districts[0].adcode
				}
				this[area] = data;
			})
		},
		getaddress(index){
			let data = {
				name:this.jiedao[index].name,
				id:this.jiedao[index].adcode
			}
			this.street = data;
			let address = {
				province:this.province.name,
				city:this.city.name,
				area:this.area.name,
				street:this.street.name
			}
			console.log(address)
			this.$emit("address",address);
			this.$emit("selectHide",false)
		},
		changeList(data){
			this.showFlag = data;
		},
		hide(){
			this.$emit("selectHide",false)
		}
	},
	created(){
		this.getGoodsList();
		this.showFlag = 'province';
	},
	watch:{
		province(val,oldVal){
			console.log(val.id)
			this.getlist('city',val.id,'shi')
		},
		city(val,oldVal){
			this.getlist('area',val.id,'qu')
		},
		area(val,oldVal){
			this.getlist('street',val.id,'jiedao')
		},
		showFlag(val,oldVal){
			this.$nextTick(()=>{
				let wrapper = document.querySelector('#'+val);
				console.log(wrapper);
				wrapper.style.height = '65vh';
				let scroll = new BScroll(wrapper,{
					click:true
				})
			})
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.select-title{
	position:relative;
	display:flex;
	justify-content:center;
	align-items:center;
	padding:1vh 0vw 2vh 0vw;
	border-bottom:1px solid #eee;
}
.close{
	position:absolute;
	width:5vw;
	height:5vw;
	right:2vw;
}
.select-header{
	display:flex;
	margin:1vh;
	font-size:3.2vw;
}
.select-header-menu{
	padding:2vw 2vw 2vw 2vw;
	text-align:center;
	overflow:hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
.select{
	color:red;
	border-bottom:1px solid red;
}
.address{
	display:flex;
	flex-flow:row wrap;
}

.select-item{
	display:flex;
	flex-flow:column nowrap;
	width:50vw;
	margin:1vw;
	overflow:hidden;
	height:70vh;
}
.select-text{
	margin:1vw 0vw;
}
.layer{
	height:80vh;
}
.fade-enter-active{
  transition: opacity 0.5s;
}
.fade-leave-active{
	transition: opacity 0s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>



