<template>
  <div class="my">
		<div class='my-information'>
			<img :src='information.avatar' class='my-information-img'>
			<div class='my-information-text'>{{information.nickname}}</div>
		</div>
    
    <!--组件  心里要有点数,显示待付款,待发货,待收货,待评价,售后-->
    <orderList></orderList>
    
		<div class='my-menus'>
			<div class='my-menu-item' v-for='(item,index) in menus' @click.stop='go(item.url)'>
				<img :src='item.imgUrl' class='my-menu-img'>
				<div class='my-menu-text'>{{item.name}}</div>
				<img src='/public/image/my/menus/前进.png' class='my-menu-go'>
			</div>
		</div>
		
    <login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import orderList from './orderList';
import { Toast } from 'mint-ui';
import login from'../login/login'
export default {
  name: 'HelloWorld',
  data () {
    return {
			information:{
			},
      menus:[
				{name:'收货地址',imgUrl:'/public/image/my/menus/地址.png',url:'address'},
				{name:'我的订单',imgUrl:'/public/image/my/menus/订单.png',url:'order'},
				{name:'购物车',imgUrl:'/public/image/my/menus/购物车.png',url:'shoppingCart'},
				//{name:'系统通知',imgUrl:'/public/image/my/menus/系统通知.png',url:''},
				//{name:'会员卡',imgUrl:'/public/image/my/menus/会员卡.png',url:''},
				{name:'优惠券',imgUrl:'/public/image/my/menus/优惠券.png',url:'coupons'},
				//{name:'积分',imgUrl:'/public/image/my/menus/积分.png',url:'commentsDetail'},
				{name:'退款订单',imgUrl:'/public/image/my/menus/订单.png',url:'returnOrder'},
			],
			otherMenus:[
				{name:'联系我们',imgUrl:'/public/image/my/menus/联系我们.png',url:''},
			],
      showFlag:false
    }
  },
	components:{
		orderList,
    login
	},
	methods:{
		go(url){
			console.log(url)
			this.$router.push({name:url,query:{key:sessionStorage.shopkey}});
		},
    getcontent(){//获取个人信息
      Indicator.open('Loading...');
      this.axios({
        url:'shopUserInfo',
        method:'get'
      }).then(res=>{
        console.log(res);
        Indicator.close();
        if(res.data.status == 200){
          this.information = res.data.data;
        }else if (res.data.status == 1001) {
          this.showFlag = true;
        }else{
          Toast(res.data.message);
        }
        
      })
    },
    login (data) {
      this.showFlag = !this.showFlag;
    }
	},
	created(){
		this.getcontent();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
	margin:0;
	padding:0;
}
.my{
	margin-bottom:15vw;
}
.my-information{
	height:30vw;
	display:flex;
	justify-content:flex-start;
	align-items:center;
	position:relative;
  border-bottom:1px solid #eee;
  background:url(/public/image/my/background.png);
  background-size:100% 100%;
}
.my-information-img{
	width:15vw;
	border-radius:50%;
	margin-left: 8vw;
}
.my-information-text{
	font-size:5vw;
	margin-left: 4vw;
	color:#aaa;
}
.my-information-go{
	position:absolute;
	right:4vw;
	width:6vw;
	color:#fff;
	font-size:8vw;
}
.my-menus{
	
}
.my-menu-item{
	display:flex;
	justify-content:flex-start;
	align-items:center;
	position:relative;
	height:12vw;
	border-bottom:1px solid #eee;
	background:#fff;
}
.my-menu-img{
	width:6vw;
	height:6vw;
	margin:1vw;
  margin-left:3vw;
}
.my-menu-text{
	font-size:3.5vw;
  font-weight:500;
}
.my-menu-go{
	position:absolute;
	right:2vw;
	width:5vw;
}
.my-otherMenus{
	margin-top:4vw;
	border-top:1px solid #eee;
	border-bottom:1px solid #eee;
}
</style>



