<template>
	<div class='activity'>
		<img @click.stop='go(item.url)' class='activity-img' v-for='(item,index) in data' :key='index' :src='item.imgUrl'>
	</div>
</template>

<script>
export default {
  name: 'activity',
  data () {
    return {
			data:[]
    }
  },
	methods:{
		getList:function(){
			this.axios({
				url:'activity',
				method:'get',
			}).then((res)=>{
				if(res.data.status == 200){
					this.data = res.data.data
				}else{
					
				}
			})
		},
		go:function(data){
			// this.$router.push({name:data})
		}
	},
	created:function(){
		this.getList();
	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.activity{
	display:flex;
	justify-content:center;
	align-items:center;
	background:#f4f5f9;
	flex-flow:row wrap;
	margin-top: 2vw;
	padding-top:2vw;
	padding-bottom:2vw;
}
.activity-img{
	width: 45vw;
	height:22.5vw;
	margin:1vw;
	border-radius:1vw 1vw;
}
</style>



