<template>
  <div class='meuns'>
		<div class='menus-item' @click.stop='go(item.url,item.id)' v-for='(item,index) in list'>
      <img class='img' :src='item.src'/>
      <div>{{item.name}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      list:[
        {name:'待付款', src:'/public/image/my/orderList/pay.png', url:'order', id:'0'},
        {name:'待发货', src:'/public/image/my/orderList/delivery.png', url:'order', id:'1'},
        {name:'待收货', src:'/public/image/my/orderList/get.png', url:'order', id:'3'},
        {name:'待评价', src:'/public/image/my/orderList/ok.png', url:'order', id:'6'},
        {name:'售后订单', src:'/public/image/my/orderList/deliveried.png', url:'returnOrder', id:'0'},
      ]
    }
  },
	components:{
		
	},
	methods:{
		go(url,id){
			console.log(url)
			this.$router.push({name:url,query:{id:id}});
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
*{
	margin:0;
	padding:0;
}
.meuns{
  display:flex;
  justify-content:space-around;
  align-items:center;
  padding:2vw 0;
  margin:1vw 0;
  border-bottom:1px solid #eee;
  
  .menus-item{
    display:flex;
    flex-flow:column wrap;
    justify-content:center;
    align-items:center;
    flex:1;
    font-size:3vw;
    
    .img{
      width:10vw;
      height:10vw;
    }
    
  }
  
}

</style>



