<template>
  <div class="orderHeader">
    <div class='overflowx'>
      <div 
        class='menus' 
        v-for='(item,index) in data' 
        :key='item.id' 
        :class='indexFlag == index ? "select":""' 
        @click.stop='change(index)'>{{item.name}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'orderHeader',
  data () {
    return {
      data:[
				{name:'全部',id:8},
				{name:'待付款',id:0},
				{name:'待发货',id:1},
				{name:'待收货',id:3},
        {name:'待评价',id:6},
        {name:'已完成',id:7},
			],
			indexFlag:0
    }
  },
	components:{
		
	},
	methods:{
		change(index){
			this.indexFlag = index;
			this.$emit('id',this.data[index].id);
		}
	},
	created(){
    if(this.$route.query.id == undefined){
      this.$emit('id',this.data[0].id)
    }else{
      this.$emit('id',this.$route.query.id)
      if(this.$route.query.id == 0) this.indexFlag = 1;
      if(this.$route.query.id == 1) this.indexFlag = 2;
      if(this.$route.query.id == 3) this.indexFlag = 3;
      if(this.$route.query.id == 6) this.indexFlag = 4;
    }
    
	},
	watch:{
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.orderHeader{
	padding:2vw;
	margin-top:9vw;
  overflow:hidden;
  height:10vw;
  width:96vw;
  position:fixed;
  top: -1vw;
  background:#fff;
  border-bottom:1px solid #eee;
}
.overflowx{
  width:96vw;
  height:15vw;
  display:flex;
  justify-content:center;
  align-items:center;
}
.menus{
  padding:2vw;
  font-size:3.5vw;
  flex:1;
  text-align:center;
}
.select{
	border-bottom:1px solid red;
	color:red;
}
</style>



