<template>
  <div class="details">
    <div class='details-title'>商品详情</div>
    <div class='details-info' v-html='goodsInfo'></div>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  name: 'goodsDetail-details',
  data () {
    return {
			goodsInfo:''
    }
  },
	methods:{
    /**
     * 获取商品详情
    */
		getData:function(){
			this.axios({
        url:`shopGoodsinfo/${this.$route.query.id}`
      }).then(res => {
        if(res.data.status == 200){
          this.goodsInfo = res.data.data.detail_info;
        }
      })
		}
	},
	created(){
		this.getData();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.details{
	background:#fff;
	margin-top:1vw;
	margin-bottom:1vw;
	display:flex;
	flex-flow:column wrap;
  .details-title{
    padding:2vw 4vw;
    border-bottom:1px solid #eee;
  }
}
</style>



