<template>
  <div v-if='showFlag' class="searchHistory">
    <div v-for='(item,index) in data' :key='index' class='searchHistory-item' @click.top.once='get(item)'>
      {{item}}
    </div>
    <img src='/public/image/search/del.png' class='del' @click='del'>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
export default {
  name: 'searchHistory',
  props:['showFlag'],
  data () {
    return {
      title:'搜索',
			conditions:'',
      data:'',
    }
  },
	components:{
		headhistory
	},
	methods:{
		get(data){
      this.$emit('data',data);
    },
    del(){
      console.log('aaaaaa')
      localStorage.searchList = '';
      this.data = '';
    }
	},
	created(){
		this.data = Array.from(new Set(localStorage.searchList.split(',')));
    console.log(Array.from(this.data))
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.searchHistory{
  display:flex;
  justify-content:flex-start;
  width:96vw;
  align-items:center;
  flex-flow:row wrap;
  position:relative;
  overflow:hidden;
  border-top:1px solid #eee;
  margin-top:2vw;
  
  .searchHistory-item{
    padding:2vw;
    border:1px solid #ddd;
    margin:2vw;
    font-size:3.5vw;
    font-weight:600;
    overflow:hidden;
  }
  
  .del{
    position:fixed;
    bottom:2vw;
    left:46vw;
    width:8vw;
    height:8vw;
  }
}
</style>



