<template>
  <div v-if='showFlag' class="searchList">
    <div class='searchList-scroll'>
      <div>
        <div v-for='(item,index) in data' class='searchList-item' @click.stop.once='showData(item)'>
          {{item}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
import BScroll from 'better-scroll'
export default {
  name: 'searchList',
  props:['keywords'],
  data () {
    return {
      title:'搜索',
			conditions:'',
      data:'',
      showFlag:false
    }
  },
	components:{
		headhistory
	},
	methods:{
		choose(data){
      console.log(data);
    },
    getdata(data){
      this.axios({
        url:'http://192.168.188.61:3000/getsearchdata',
        params:{
          data:data
        }
      }).then( res => {
        this.data = res.data.data;
        this.showFlag = true;
      })
    },
    showData(data){
      console.log(data);
      this.$emit('data',data);
      let localdata = '';
      try{
        localdata = localStorage.searchList.split(',');
      }catch(err){
        localdata = [];
      }
      
      localdata.push(data);
      console.log('aaaaaaaa',new Set(localdata))
      localStorage.searchList = new Set(localdata);
      this.showFlag = false;
    }
	},
	created(){
		
	},
  watch:{
    keywords(val){
      if(val.trim().length != 0){
        this.getdata(val);
      }else{
        this.data = ''
      }
    },
    data(val){
      this.$nextTick(()=>{
				let wrapper = document.querySelector('.searchList-scroll');
				console.log(wrapper);
				wrapper.style.height = '86vh';
				let scroll = new BScroll(wrapper,{
					click:true
				})
			})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.searchList{
  position:fixed;
  left:0;
  width:96vw;
  padding:0vw 2vw;
  z-index:999;
  top:26vw;
  height:88vh;
  background:#fff;
  border-top:1px solid #eee;
  padding:2vw;
  border-top-left-radius:2vw;
  border-top-right-radius:2vw;
  .searchList-scroll{
    overflow:hidden;
    .searchList-item{
      padding:3vw 0vw;
      border-bottom:1px solid #eee;
    }
  }
  
}
</style>



