<template>
  <div class="returnOrder">
		<headhistory :data='title'></headhistory>
    
    <div class='menu'>
      <div 
        v-for='(item,index) in menus' 
        :class='index == selectMenu ? "select":""'
        @click.stop='select(index)'
      >{{item.name}}</div>
    </div>
    
    <div class='returnOrder-item' v-for='(item,index) in goodsData'>
      <showGood :goods='item.order'></showGood>
      <div class='returnOrder-item-text'>
        <!--退款退货-->
        <div v-if='item.after_status == 0 && item.status != 4' class='returnOrder-item-text-ok'>非售后订单</div>
        <div v-if='item.after_status == 1 && item.status != 4' class='returnOrder-item-text-ok'>处理中</div>
        <div v-if='item.after_status == 2 && item.status != 4' class='returnOrder-item-text-ok'>商家已拒绝您的退款申请,请尽快联系商家客服，咨询具体原因</div>
        <div v-if='item.after_status == 3 && item.status != 4' class='returnOrder-item-text-ok'>商家已同意退货，稍后会将订单金额退还至您的账户</div>
        <div v-if='item.after_status == 4 && item.status != 4' class='returnOrder-item-text-ok'>商家已拒绝您的退款申请,请尽快联系商家客服，咨询具体原因</div>
        <div v-if='item.after_status == 5 && item.status != 4' class='returnOrder-item-text-ok'>处理中</div>
        <div v-if='item.after_status == 6 && item.status != 4' class='returnOrder-item-text-ok'>商家已同意退货，请尽快将货退回，并且填写退货快递单号</div>
        <div v-if='item.after_status == 7 && item.status != 4' class='returnOrder-item-text-ok'>等待商户确认收货</div>
        <div v-if='item.status == 4' class='returnOrder-item-text-ok'>已完成</div>
      </div>
      <div class='returnOrder-item-bar'>
        
        <div v-if='item.after_status == 3 && item.status != 4' class='returnOrder-item-bar-btn'>退款中</div>
        
        <!--商家未处理订单-->
        <div v-if='item.after_status == 1 || item.after_status == 5 && item.status != 4' class='returnOrder-item-bar-btn'>处理中</div>
        
        <!--商家同意退货但是没有填写快递单号-->
        <div v-if='item.after_status == 6 && item.status != 4' class='returnOrder-item-bar-btn' @click='fullOrder(item.order_sn)'>填写快递单号</div>
        
        <!--只有在商家未同意的情况下才可撤销申请-->
        <!--<div v-if='item.after_status == 1 || item.after_status == 2 && item.status != 4' class='returnOrder-item-bar-btn' @click='undoReturn(item.order_sn)'>撤销退货</div>-->
        
        <!--只有在商家不同意退款或者未处理时才能重新填写退款理由-->
        <div v-if='item.after_status == 2 || item.after_status == 4 && item.status != 4' class='returnOrder-item-bar-btn'>重新填写退款理由</div>
        
        
        <!--退货退款同意退款且填写完快递单号-->
        <div v-if='item.after_status == 7 && item.status != 4' class='returnOrder-item-bar-btn'>待商家确认收货</div>
        
        <!--已退款-->
        <div v-if='item.status == 4 ' class='returnOrder-item-bar-btn'>已退款</div>
      </div>
    </div>
    
    <login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import headhistory from'../history/headhistory'
import showGood from'../applicationForDrawback/showGood'
import { MessageBox } from 'mint-ui';
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui'
import login from'../login/login'
export default {
  name: 'returnOrder',
  data () {
    return {
      title:'售后订单',
			goodsData:'',
      localStr:'',
      sessionSt:'',
      menus:[
        {name:'全部',id:'0'},
        {name:'处理中',id:'1'},
        {name:'已同意',id:'2'},
        {name:'已完成',id:'3'},
      ],
      selectMenu:0,
      showFlag:false
    }
  },
	components:{
		headhistory,
		showGood,
    login
	},
	
	created(){
    if(sessionStorage.jwt == undefined || sessionStorage.jwt == 'undefined' || sessionStorage.jwt.length == 0){
      this.login();
      return false
    }
		this.getData(0);
	},
	watch:{
		
	},
  methods:{
    select(index){
      this.selectMenu = index;
      this.getData(index);
    },
    /**
     * 获取数据
    */
		getData(id){
      Indicator.open('Loading...');
      this.axios({
        url:'shopOrderAfterList',
        params:{
          status:id
        }
      }).then(res => {
        console.log('获取',res);
        Indicator.close();
        this.goodsData = res.data.data;
      })
    },
    /**
     * 填写快递单号
     * after_express_number: 快递状态
     * order_sn: 订单号
     * type: 1代表微信 2代表小程序
    */
    fullOrder(order_sn){
      MessageBox.prompt('请输入快递单号').then(({ value, action }) => {
        console.log(value,action);
        Indicator.open('Loading...');
        let data = {
          after_express_number:value,
          order_sn:order_sn,
          type:1
        }
        this.axios({
          url:'shopOrderAfter',
          method:'put',
          data:this.qs.stringify(data)
        }).then(res => {
          console.log(res);
          Indicator.close();
          if(res.data.status == 200){
            Toast('提交成功');
            this.getData(0);
          }else{
            Toast(res.data.status);
          }
          
        })
      });
    },
    /**
     * 取消退款接口
    */
    undoReturn(order_sn){ //取消退款
      console.log(order_sn);
      this.axios({
        url:`shopOrderUnAfter/${order_sn}`,
        method:'put'
      }).then(res => {
        console.log(res);
        Toast(res.data.status);
      })
    },
    login (data) {
      this.showFlag = !this.showFlag;
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.returnOrder{
  padding:24vw 2vw 1vw 2vw;
  background:#f2f2f2;
  
  .menu{
    position:fixed;
    display:flex;
    background:#ffffff;
    width:100vw;
    height:10vw;
    left:0;
    top:12vw;
    justify-content:space-between;
    align-items:center;
    
    div{
      width:100%;
      height:90%;
      display:flex;
      align-items:center;
      justify-content:center;
      box-sizing:border-box;
      font-size:3vw;
    }
    .select{
      border-bottom:1px solid red;
      color:red;
    }
  }
  
  .returnOrder-item{ //订单数据
    background:#fff;
    margin-bottom:2vw;
    
    .returnOrder-item-bar{
      border-top:1px solid #eee;
      padding:2vw;
      display:flex;
      justify-content:flex-end;
      align-items:center;
      
      .returnOrder-item-bar-btn{
        padding:2vw 4vw;
        font-size:3vw;
        border:1px solid #888;
        margin-left:2vw;
        border-radius:5vw;
        color:#000;
      }
      
    }
    .returnOrder-item-text{
      font-size:3vw;
      margin:0vw 2vw 1vw 2vw;
      
      .returnOrder-item-text-err{
        color:red;
      }
      
      .returnOrder-item-text-ok{
        color:#555;
      }
      
    }
    
  }
  
}
</style>



