<template>
  <div class="coupons">
		<headhistory :data='title' v-if='true'></headhistory>
    <div v-if='hongbao' id='scroll'>
      <div>
        <div class='coupons-item' v-for='(item,index) in hongbao' :key='item.id' @click='go()'>
          <div class='left'>
            <div>￥<span>{{item.price}}</span>{{item.type_name}}</div>
            <div>订单金额满{{item.full_price}}元可用</div>
            <div>{{item.start_time}}-{{item.end_time}}</div>
          </div>
          <div class='right'>立即使用</div>
        </div>
      </div>
    </div>
    <div v-if='!hongbao' class='coupons-err'>
      暂无红包
    </div>
    
    <login @close='login' :showFlag='showFlag'></login>
  </div>
</template>

<script>
import headhistory from '../history/headhistory' 
import BScroll from 'better-scroll'
import login from'../login/login'
export default {
  name: 'coupons',
  data () {
    return {
			title:'红包',
      hongbao:'',
      showFlag:false
    }
  },
	components:{
		headhistory,
		login
	},
	methods:{
		getData(id){
      this.axios({
        url:'ShopVoucher'
      }).then(res => {
        console.log(res);
        this.hongbao = res.data.data;
      })
    },
    go(){
      this.$router.push({name:'index'})
    },
    login (data) {
      this.showFlag = !this.showFlag;
    }
	},
	created(){
    if(sessionStorage.jwt == undefined || sessionStorage.jwt == 'undefined' || sessionStorage.jwt.length == 0){
      this.login();
      return false
    }
		this.getData(1);
	},
  watch:{
    hongbao(val){
      this.$nextTick(()=>{
				let wrapper = document.querySelector('#scroll');
				console.log(wrapper);
				wrapper.style.height = '92vh';
				let scroll = new BScroll(wrapper,{
					click:true
				})
			})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.coupons{
  margin-top:12vw;
  padding:2vw;
  background:#fff;
  min-height:91vh;
}
.coupons-err{
  text-align:center;
}
.coupons-item{
  display:flex;
  flex-flow:row nowrap;
  background:#fff2e6;
  color:#ff952e;
  border-radius:1vw;
  margin-bottom:2vw;
  .left{
    border-right:1px dashed #ffc791;
    position:relative;
    padding:2vw;
    width:60vw;
    :before{
      content:'';
      position:absolute;
      top:-2vw;
      right:-2vw;
      background:#fff;
      width:4vw;
      height:4vw;
      border-radius:50%;
    }
    :after{
      content:'';
      position:absolute;
      bottom:-2vw;
      right:-2vw;
      background:#fff;
      width:4vw;
      height:4vw;
      border-radius:50%;
    }
    div{
      font-size:3vw;
    }
    span{
      font-size:5vw;
      margin:0vw 1vw;
    }
  }
  .right{
    width:30vw;
    display:flex;
    align-items:center;
    justify-content:center;
  }
}

</style>



